<template>
	<div class="pldpageleft">
		<img class="pldalllogo" src="static/img/logo_fin.png" alt="img"/>
		<div class="pldlist">
			<ul>
				<li v-for="(i,index) in leftlist" @click.stop="pldclk($event,index)">
					<a :style="{color:i.color}">
						<img :src="'static/img/'+i.src" alt="af"/>
						{{i.pp}}
					</a>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'top',
		data(){
			return{
				leftlist:[
					{pp:'控制设备',color:'#fff',src:'device.png',lik:'#'},
					{pp:'安防设备',color:'#fff',src:'anfang.png',lik:'#'},
					{pp:'监测设备',color:'#fff',src:'jiance.png',lik:'#'},
//					{pp:'系统设置',color:'#fff',src:'setting.png',lik:'#/mains'}
				]
			}
		},
		methods:{
			'pldclk':function(e,index){
				
				this.$emit('pldreturn')
				
				let that=this
				this.pld_global.pldsevice=index
				this.$emit('getsevice',that.pld_global.roomid,this.pld_global.pldsevice)
				
				for(var i=0;i<this.leftlist.length;i++){
					this.leftlist[i].color='#fff'
					this.leftlist[i].src=this.leftlist[i].src.replace('hv','')
				}
				this.leftlist[index].color='#18d1ff'
				
				if(this.leftlist[index].src.indexOf('hv')<0){
					this.leftlist[index].src='hv'+this.leftlist[index].src
				}else{
					return
				}
			},
			'pldleftclk':function(edata){
				this.leftlist[edata].color='#18d1ff'
				this.leftlist[edata].src='hv'+this.leftlist[edata].src
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldpageleft{
	background-color:#2d3031;
	width:190px;
	padding-top: 35px;
	text-align: center;
	height: 100%;
	overflow: hidden;
	float: left;
	position: fixed;
	.pldlist{
		text-align:left;
		ul{
			padding-top: 12px;
			li{
				cursor: pointer;
				margin-top: 35px;
				a{
					padding-left: 50px;
					display: block;
					/*color: #fff;*/
					img{
						margin-right: 17px;
					}
				}
			}
		}
	}
}
</style>


