<template>
	<div class="pldequipment container-fluid">
		<div class="pldaddtop">
			<img :src="pldimg+'.png'" alt="equipment"/>
			<p>
				{{toptitle}}
			</p>
		</div>
		<div class="pldaddcontent">
			<ul>
				<li v-for="(it,index) in pldlist">
					<span>
						<img src="static/img/sanjiao.png" alt="sanjiao"/>
						{{it.pp}}：
					</span>
					<div class="listright">
						<input @input="pldipt" v-model="pldtext" type="text" placeholder="请填写名称" v-if="index==2" />
						<select v-else-if="index==0" @change="pldselet()" v-model="indexidt">
							<option v-for="(ia,idd) in listequipment" :value="ia.id">{{ia.pp}}</option>
						</select>
						<select v-else-if="index==1" @change="pldsele()" v-model="indexid">
							<option v-for="(ia,ii) in listroom"  :value="ia.id">{{ia.pp}}</option>
						</select>
						<img v-if="index==2" style="display: none;" src="static/img/bottom.png" alt="bottom.png"/>
						<img v-else src="static/img/bottom.png" alt="bottom.png"/>
					</div>
				</li>
			</ul>
			<p v-show="plderrortext.kg" class="plderror">
				{{plderrortext.pp}}
			</p>
		</div>
	</div>
</template>

<script>
	export default{
		name:'add',
		data(){
			return{
				indexid:0,
				indexidt:0,
				pldurl:this.pld_global.globalurl,
				toptitle:this.pld_global.escid.title,
				pldimg:this.pld_global.escid.img,
				pldtext:'',
				pldlist:[
					{pp:'选择设备'},
					{pp:'选择房间'},
					{pp:'填写名称'}
				],
				//设备列表
				listequipment:[
					{pp:'LTH5200-630'},
					{pp:'LTH5200-630'},
					{pp:'LTH5200-630'},
					{pp:'LTH5200-630'}
				],
				//房间列表
				listroom:[
					{pp:'主卧'},
					{pp:'主卧'},
					{pp:'主卧'},
					{pp:'主卧'}
				],
				plderrortext:{
					pp:'设备或名称重复',
					kg:false
				}
			}
		},
		methods:{
			'pldipt':function(){
				let that=this
				if(this.pldtext==''){
					//给下一步禁用
					that.$emit('plddisablet')
				}else{
					//给下一步接触禁用
					this.$emit('plddisable')
					this.pld_global.addequipment.devname=this.pldtext
				}
			},
			'pldsele':function(){
				this.pld_global.addequipment.roomid=this.listroom[this.indexid].roomid
			},
			'pldselet':function(){
				this.pld_global.addequipment.deveui=this.listequipment[this.indexidt].deveui
			}
		},
		mounted:function(){
			let that=this
			
			this.$root.eventHub.$on('pldpshow',function(){
				that.plderrortext.kg=true
			})
			
			if(this.pld_global.escid.id==3){
				this.$ajax.get(this.pld_global.globalurl+'/room/devices/allcam',{
					
				})
				.then(function(res){
					that.listequipment.splice(0,that.listequipment.length)
					for(let i=0;i<res.data.Data.length;i++){
						let pldlist={pp:'',id:'',deveui:''}
						pldlist.pp=res.data.Data[i].Name
						pldlist.id=i
						pldlist.deveui=res.data.Data[i].DevUID
						that.listequipment.push(pldlist)
					}
					that.pld_global.addequipment.deveui=that.listequipment[0].deveui
				})
				.catch(function(){
					
				})
			}else if(this.pld_global.escid.id==23){
				this.$ajax.get(this.pld_global.globalurl+'/room/devices/allcam',{
					
				})
				.then(function(res){
					that.listequipment.splice(0,that.listequipment.length)
					for(let i=0;i<res.data.Data.length;i++){
						let pldlist={pp:'',id:'',deveui:''}
						pldlist.pp=res.data.Data[i].Name
						pldlist.id=i
						pldlist.deveui=res.data.Data[i].DevUID
						that.listequipment.push(pldlist)
					}
					that.pld_global.addequipment.deveui=that.listequipment[0].deveui
				})
				.catch(function(){
					
				})
			}else{
				this.$ajax.get(this.pld_global.globalurl+'/room/devices/loraServerDevices',{
					
				})
				.then(function(res){
					that.listequipment.splice(0,that.listequipment.length)
					for(let i=0;i<res.data.Data.length;i++){
						let pldlist={pp:'',id:'',deveui:''}
						pldlist.pp=res.data.Data[i].name
						pldlist.id=i
						pldlist.deveui=res.data.Data[i].devEUI
						that.listequipment.push(pldlist)
					}
					that.pld_global.addequipment.deveui=that.listequipment[0].deveui
				})
				.catch(function(){
					
				})
			}
			
			this.$ajax.get(this.pld_global.globalurl+'/room',{
				
			})
			.then(function(res){
				that.listroom.splice(0,that.listroom.length)
				for(let i=0;i<res.data.Data.length;i++){
					let pldlist={pp:'',id:'',roomid:''}
					pldlist.pp=res.data.Data[i].Name
					pldlist.id=i
					pldlist.roomid=res.data.Data[i].id
					that.listroom.push(pldlist)
				}
				that.pld_global.addequipment.roomid=that.listroom[0].roomid
			})
			.catch(function(){
				
			})
			
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	.pldaddtop{
		border: 1px solid #dddddd;
		text-align:center;
		width:172px;
		margin:0 auto;
		margin-bottom:30px;
		img{
			margin-top: 25px;
			width: 120px;
			height: 120px;
		}
		p{
			margin-top: 23px;
			margin-bottom: 13px;	
		}
	}
	.pldaddcontent{
		ul{
			li{
				padding-left: 30px;
				line-height: 40px;
				margin-bottom: 20px;
				img{
					position: relative;
					top: -3px;
					right: 3px;
				}
				span{
					float: left;
					width: 90px;
				}
				.listright{
					position: relative;
					margin-left: 80px;
					input,select{
						width: 98%;
						height: 40px;
						border: 1px solid #dbdbdb;
						border-radius: 6px;
						padding-left: 15px;
					}
					img{
						position: absolute;
						right: 25px;
    					top: 17px;
					}
				}
			}
		}
		.plderror{
			margin-left: 120px;
			color: red;
		}
	}
}
</style>


