<template>
	<div class="pldequipment container-fluid">
		<img src="static/img/delete.png" alt="deletes"/>
		<p>
			{{plddeletetitle}}
		</p>
	</div>
</template>

<script>
	export default{
		name:'deletes',
		data(){
			return{
				plddeletetitle:'确定删除设备吗？删除后不可恢复！'
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	text-align: center;
	img{
		margin-top: 40px;
	}
	p{
		margin-top: 18px;
		margin-bottom: 50px;
		color: #f58282;
		font-size: 18px;
	}
}
</style>


