<template>
</template>

<script>
	//通用设置中ipt默认值
	let pldiptval=''
	
	//分页所需参数
	let pldts=10
	let pldoffset=0
	let deviceid=0
	
	let pldnamemore=''
	
	let title=''
	
	let plduid=0
	
	let agreementid=0
	
	//头部点击过几次（默认0次）
	const pldclkmath=0
	//默认警告框关闭
	const pldshow=true
	
//	const globalurl='http://192.168.181.60:8999'
//	const globalwsurl='192.168.181.60:8999'
	
	const globalurl=window.location.protocol+'//'+window.location.host
	const globalwsurl=window.location.host
	const loginid=''
	const logincookie=''
	const loginpermission=''
	const loginuser=''
	const roomid=1
	//设备类型常量
	const pldsevice=0
	const escid={
		id:0,
		title:'',
		img:''
	}
	//报警数组
	const errorlist=[
		
	]
	//添加设备的变量
	const addequipment={
		agreement:'',
		roomid:'',
		deveui:'',
		devname:'',
		status:true
	}
	
	let that=this
	//公共方法-判断应该调用哪个弹窗
	let pldusetc=function(cc,dd){
		switch (cc){
			case 2:
				return dd[1]
			case 1:
				return dd[3]
			case 23:
				return dd[0]
			default:
				return dd[2]
		}
	}
	
	//时间撮转成日期格式
	
	let add0 = function(m){
    	return m < 10 ? '0' + m: m
    }
	
	let dateFormat = function(timestamp){
	    var time = new Date(timestamp * 1000)    //先将时间戳转为Date对象，然后才能使用Date的方法
	    var year = time.getFullYear(),
	        month = time.getMonth() + 1 ,  //月份是从0开始的
	        day = time.getDate(),
	        hour = time.getHours(),
	        minute = time.getMinutes(),
	        second = time.getSeconds()
	        //add0()方法在后面定义
	    return  year+'-'+add0(month)+'-'+ add0(day)+'  '+add0(hour)+':'+add0(minute)+':'+add0(second)  
    }
	
	export default{
		globalurl,
		loginid,
		logincookie,
		loginpermission, 
		loginuser,
		escid,
		addequipment,
		roomid,
		pldsevice,
		pldshow,
		pldusetc,
		pldts,
		pldoffset,
		deviceid,
		agreementid,
		dateFormat,
		pldnamemore,
		title,
		globalwsurl
	}
</script>

<style>
</style>


