<template>
	<div class="pldequipment container-fluid">
		<ul>
			<li v-for="(it,index) in temperature.namelists">
				<span class="pldleft">
					<img src="static/img/sanjiao.png" alt="sanjiao" v-show="it.pldshow"/>
					<span>
						{{it.pp}}：
					</span>
				</span>
				<div v-if="index==1" class="pldlidiv pldvall">
					<select @change="pldsel" v-model="pldvv">
						<option v-if="index==1" v-for="ita in temperature.optionlist[index]">{{ita.pp}}</option>
						<!--<option v-if="index==2" v-for="itaa in temperature.optionlist[index-1]">{{itaa.pp}}</option>-->
					</select>
					<img src="static/img/bottom.png" alt="bottom"/>
				</div>
				<div v-else-if="index==0" class="pldvall">
					<input @input="pldsett" type="text" v-model="pldallsj" :placeholder="it.pla" />
				</div>
			</li>
		</ul>
		<div class="pldvalldiv">
			<p>
				(<img src="static/img/heisanjiao.png" alt="heisanjiao"/>
				{{temperature.pld}}
			</p>
		</div>
	</div>
</template>

<script>
	export default{
		name:'temperature',
		data(){
			return{
				pldallsj:'',
				pldvv:'',
				temperature:{
					namelists:[
//						{pp:'',pldshow:false},
						{pp:'修改名称',pldshow:true,pla:'请输入要修改名称'},
						{pp:'关联设备',pldshow:true}
					],
					pld:'注意，只能关联同一房间的设备，若该房间无设备，请自行添加)',
					optionlist:[
						[
//							{pp:'主卧'},
//							{pp:'次卧'},
//							{pp:'客厅'},
//							{pp:'浴室'}
						],
						[
						]
					]
				}
			}
		},
		methods:{
			'pldsett':function(){
				this.$root.eventHub.$emit('plddis')
			},
			'pldsel':function(){
				let that=this
				for(let i=0;i<that.temperature.optionlist[1].length;i++){
					if(that.pldvv==that.temperature.optionlist[1][i].pp){
						that.pld_global.plduid=that.temperature.optionlist[1][i].id
					}
				}
			}
		},
		mounted:function(){
			
			let that=this
			this.$root.eventHub.$emit('plddismr')
			this.$root.eventHub.$on('pldfssj1',function(){
				that.pld_global.pldiptval=that.pldallsj
			})
				this.$ajax.get(this.pld_global.globalurl+'/room/devices',{
					params:{
						roomID:that.pld_global.roomid
					},
			      	headers:{
			      		'Accept': 'application/json',
			      		"maxiiot_user" :this.pld_global.logincookie
			      	}
				})
				.then(function(res){
					for(let i=0;i<res.data.Data.length;i++){
						let pldtest={pp:'',id:''}
						pldtest.pp=res.data.Data[i].DevName
						pldtest.id=res.data.Data[i].ID
						that.temperature.optionlist[1].push(pldtest)
					}
					that.pldvv=res.data.Data[0].DevName
					that.pld_global.plduid=res.data.Data[0].ID
				})
				.catch(function(){
					
				})
			
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	height: 400px;
	.pldvalldiv{
		img{
			position: relative;
			top: -6px;
			right: -2px;
		}
		padding-left:8%;
		color:#a9a9a9;
	}
	ul{
		li{
			overflow: hidden;
			margin-bottom: 20px;
			.pldleft{
				display: block;
				width: 7%;
				text-align: right;
				float: left;
				margin-right:10px;
				line-height:40px;
				img{
					position: relative;
					top: -3px;
					right: -2px;
				}
			}
			.pldlidiv{
				position: relative;
				img{
					position: absolute;
					right: 30px;
					top: 16px;
				}
			}
			.pldvall{
				float: left;
				width: 90%;
			}
			input,select{
				border-radius: 6px;
				padding-left: 15px;
				width: 100%;
				height: 40px;
				border: none;
				border: 1px solid #dbdbdb;
				outline: none;
			}
			.pldipt{
				input{
					width: 49%;
				}
			}
		}
	}
}
</style>


