<template>
	<div class="pldequipment container-fluid">
		<ul>
			<li v-for="(it,index) in pldlist" class="col-md-3 col-xs-3 col-sm-3">
				<div class="pldlidiv" :class="it.cla" @click.stop="pldclkli(index)">
					<img :src="it.add+'.png'" alt="add" />
					<p>
						{{it.pp}}
					</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				pldlist:[
					{pp:'智能设备1',add:'/static/img/equipment.png'},
					{pp:'智能设备1',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'},
					{pp:'智能设备',add:'/static/img/equipment.png'}
				],
				pldequipment:0,
				pldurl:this.pld_global.globalurl
			}
		},
		methods:{
			'pldclkli':function(data){
				let that=this
				for(let i=0;i<this.pldlist.length;i++){
					that.pldlist[i].cla=''
				}
				that.pldlist[data].cla='pldznmore'
				that.pldequipment=that.pldlist[data].id
				//给下一步接触禁用
				that.$emit('plddisable')
				this.pld_global.escid.title=that.pldlist[data].pp
				this.pld_global.escid.img=that.pldlist[data].add
				
				//判断是否是loraserver设备
				if(parseInt(that.pldequipment)==3){
					this.pld_global.escid.id=3
				}else if(parseInt(that.pldequipment)==23){
					this.pld_global.escid.id=23
				}else{
					this.pld_global.escid.id=0
				}
				this.pld_global.addequipment.agreement=that.pldequipment
			}
		},
		mounted:function(){
			let that=this
			
			//获取控制设备
			this.$ajax.get(this.pld_global.globalurl+'/room/agreements',{
				params:{
					type:1
				},
		      	headers:{
		      		'Accept': 'application/json',
		      		"maxiiot_user" :this.pld_global.logincookie
		      	}
			})
			.then(function(res){
	      		that.pldlist.splice(0,that.pldlist.length)
	      		for(let i=0;i<res.data.Data.length;i++){
	      			let pldaddlist={
	      				pp:'智能设备',add:'static/img/equipment.png',id:'',cla:''
	      			}
	      			pldaddlist.pp=res.data.Data[i].Name
	      			pldaddlist.add='static/img/addimg/'+res.data.Data[i].ID
	      			pldaddlist.id=res.data.Data[i].ID
	      			that.pldlist.push(pldaddlist)
	      		}
	      	})
	      	.catch(function(){
	      		
	      	})
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	height: 420px;
	overflow-y: auto;
	ul{
		overflow: hidden;
		li{
			padding-right: 20px;
			padding-top: 3px;
			margin-bottom: 20px;
			.pldlidiv{
				cursor: pointer;
				border: 1px solid #dddddd;
				overflow:hidden;
				text-align:center;
				img{
					margin-top: 20px;
					width: 120px;
    				height: 120px;
				}
				p{
					margin-top: 20px;
					margin-bottom: 15px;
					float: left;
					width: 100%;
					text-align: center;
				}
			}
			.pldznmore{
				position: relative;
				top: -3px;
				border: 1px solid #18D1FF;
			}
		}
	}
}
</style>


