<template>
	<div class="pldmaskall">
		<div class="pldmask">
		
		</div>
		<div class="pldmasktc">
			<div class="pldmasktop">
				<span>
					{{masks[0][0].title}}
				</span>
				<img src="static/img/error.png" alt="error" @click="pldclose"/>
			</div>
			<div class="pldcontent container-fluid">
				<div class="pldcontentzj" v-show="masks[3][0].title">
					<button v-for="(ia,index) in masks[1]" :data_index="index" @click="pldbtn(index)" :class="ia.cla">
						{{ia.title}}
					</button>
				</div>
				<components :is="pldmaskcom" @plddisable='plddis' @plddisablet='plddist'></components>
			</div>
			<div class="pldmaskfin">
				<div class="pldmaskfinbtn">
					<button v-for="(it,index) in masks[2]" @click="pldclko(index)" :disabled="it.dd" v-if="it.dd==true" style="cursor: no-drop;" :class="it.cla">
						{{it.title}}
					</button>
					<button @click="pldclko(index)" :disabled="it.dd" v-else :class="it.cla">
						{{it.title}}
					</button>
					<!--<button v-for="(it,index) in masks[2]" v-if="index==1" @click="pldclkt" :class="it.cla">
						{{it.title}}
					</button>-->
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import allequipment0 from '@/components/equipment/allequipment'
	import allequipment1 from '@/components/equipment/afequipment'
	import allequipment2 from '@/components/equipment/jcequipment'
	import add from '@/components/equipment/addequipment'
	import deletes from '@/components/common_model/delete'
	import temperature0 from '@/components/common_model/updatadate'
	import temperature1 from '@/components/common_model/agodate'
	import temperature2 from '@/components/common_model/temperature'
	import allsetting0 from '@/components/common_model/peopledate'
	import allsetting1 from '@/components/common_model/allsetting'
	import allsetting2 from '@/components/common_model/commonsetting'
	export default{
		name:'maskl',
		props:['masks','pldmaskcom','plddis','plddist'],
		data(){
			return{
			}
		},
		components:{
			allequipment0,
			allequipment1,
			allequipment2,
			add,
			deletes,
			temperature0,
			temperature1,
			temperature2,
			allsetting1,
			allsetting0,
			allsetting2
		},
		methods:{
			'pldclose':function(){
				this.$emit('pldcls')
			},
			'pldbtn':function(index){
				this.$emit('pldchange',index,this.masks[1][index].pldfunc)
			},
			//第二个按钮点击
			'pldclko':function(index){
				this.$emit('pldnext',index,this.masks[2][index].pldfunc)
//				this.$emit('pldclose')
			},
			//第一个按钮点击
			'pldclkt':function(){
				this.$emit('pldclose')
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldmaskall{
	position: relative;
	overflow: hidden;
	height: 100%;
	width: 100%;
	.pldmask{
		width: 100%;
		height: 100%;
		background-color: #000;
	    opacity: .6;
	    position: fixed;
	    top: 0;
	    left: 0;
	    z-index: 13;
	}
	.pldmasktc{
		position: fixed;
	    background-color: #fff;
	    width: 67%;
	    z-index: 14;
	    margin: 0 auto;
	    top: 10%;
	    left: 20%;
		.pldmasktop{
			overflow:hidden;
			padding:15px;
			border-bottom:1px solid #eeeeee;
			span{
				font-size: 16px;
				float: left;				
			}
			img{
				display: inline-block;
			    padding: 20px;
			    position: absolute;
			    right: 0px;
			    top: 0px;
				cursor: pointer;
				float: right;
			}
		}
		.pldcontent{
			padding: 20px;
			margin: 0 auto;
			.pldcontentzj{
				display:table;
				margin:0 auto;
				margin-bottom:22px;
				button{
					background-color: transparent;
					width: 120px;
					height: 32px;
					border: 1px solid #e4e4e4;
				}
				.pldcla{
						background-color: #18d1ff;
						color: #fff;
					}
				}
		}
		.pldmaskfin{
			padding-top: 10px;
			.pldmaskfinbtn{
				border-top: 1px solid #eeeeee;
				overflow:hidden;
				padding-top:10px;
				padding-right:20px;
				padding-bottom:10px;
				button{
					float: right;
					margin-left: 10px;
					width: 103px;
					height: 40px;
					border-radius: 300px;
					outline: none;
					border: 1px solid #c5c5c5;
					background-color: transparent;
					font-size: 16px;
				}
				.pldclahide{
					display: none;
				}
				.pldcla{
					background: -webkit-linear-gradient(left,#8652ff,#1bd1f5);
					color: #fff;
				}
				.pldhide{
					display: none;
				}
			}
		}
	}
}
</style>


