<template>
	<div class="pldequipment container-fluid">
		<ul>
			<li v-for="(it,index) in temperature.namelists">
				<span class="pldleft">
					<img src="static/img/sanjiao.png" alt="sanjiao" v-show="it.pldshow"/>
					<span>
						{{it.pp}}
					</span>
				</span>
				<div v-if="index==2" class="pldlidiv pldvall">
					<select>
						<option v-if="index==0" v-for="ita in temperature.optionlist[index]">{{ita.pp}}</option>
						<option v-if="index==2" v-for="itaa in temperature.optionlist[index-1]">{{itaa.pp}}</option>
					</select>
					<img src="static/img/bottom.png" alt="bottom"/>
				</div>
				<div v-else-if="index==1" class="pldvall">
					<input @input="pldsett" v-model="it.val" type="text" :placeholder="it.pla" />
				</div>
				<div v-else-if="index==0" class="pldvall">
				</div>
				<div class="pldipt pldvall" v-else>
					<input @input="pldsett" type="text" v-model="temperature.pld[index-3].val" :placeholder="temperature.pld[index-3].pla" />
					－
					<input @input="pldsett" type="text" v-model="temperature.pld[index-3].valo" :placeholder="temperature.pld[index-3].plao" />
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		name:'temperature',
		data(){
			return{
				temperature:{
					namelists:[
						{pp:'',pldshow:false},
						{pp:'修改名称：',pldshow:true,pla:'请输入要修改名称',val:''},
						{pp:'选择设置：',pldshow:true},
						{pp:'温度：',pldshow:false},
						{pp:'湿度：',pldshow:false}
					],
					pld:[
						{pla:'请输入最小温度值',val:'',valo:'',plao:'请输入最大温度值'},
						{pla:'请输入最小湿度值',val:'',valo:'',plao:'请输入最大湿度值'}
					],
					optionlist:[
						[
							{pp:'主卧'},
							{pp:'次卧'},
							{pp:'客厅'},
							{pp:'浴室'}
						],
						[
							{pp:'设置报警阀值'},
							{pp:'设置报警阀值'},
							{pp:'设置报警阀值'},
							{pp:'设置报警阀值'}
						]
					]
				}
			}
		},
		methods:{
			'pldsett':function(){
				let that=this
				if(that.temperature.namelists[1].val!=''&&that.temperature.pld[0].val!=''&&that.temperature.pld[0].valo!=''&&that.temperature.pld[1].val!=''&&that.temperature.pld[1].valo!=''){
					this.$root.eventHub.$emit('plddis')
				}
			}
		},
		mounted:function(){
			let that=this
			this.$root.eventHub.$on('pldbaocun',function(dd,cc,ll){
				this.$ajax.post(this.pld_global.globalurl+'/downLinks',{
					devEUI:dd,
					type:5,
					data:[
						that.temperature.pld[0].valo,that.temperature.pld[1].valo,that.temperature.pld[1].val,that.temperature.pld[0].val
					]
				})
				.then(function(ll){
//					ll()
				})
				.catch(function(){
					alert('指令下行不成功')
				})
			})
			this.$root.eventHub.$on('pldfssj2',function(){
				that.pld_global.pldiptval=that.temperature.namelists[1].val
			})
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	ul{
		li{
			overflow: hidden;
			margin-bottom: 20px;
			.pldleft{
				display: block;
				width: 7%;
				text-align: right;
				float: left;
				margin-right:10px;
				line-height:40px;
				img{
					position: relative;
					top: -3px;
					right: -2px;
				}
			}
			.pldlidiv{
				position: relative;
				img{
					position: absolute;
					right: 30px;
					top: 16px;
				}
			}
			.pldvall{
				float: left;
				width: 90%;
			}
			input,select{
				border-radius: 6px;
				padding-left: 15px;
				width: 100%;
				height: 40px;
				border: none;
				border: 1px solid #dbdbdb;
				outline: none;
			}
			.pldipt{
				input{
					width: 49%;
				}
			}
		}
	}
}
</style>


