<template>
	<div class="pldsmartswitch">
		<img src="static/img/smart_switch.png" alt="smart_switch"/>
		<div v-for="(it,index) in pldswitch" class="pldpp">
			<span>
				{{it.title}}
			</span>
			<div class="pldsectopi"><i ></i></div>
			<span>
				{{it.pp}}
			</span>
		</div>
	</div>
</template>

<script>
	export default{
		name:'smartSwitch',
		data(){
			return{
				pldswitch:[
					{
						title:'开关K1',pp:'已开启'
					},
					{
						title:'开关K2',pp:'已开启'
					},
					{
						title:'开关K3',pp:'已关闭'
					}
				]
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.pldsmartswitch{
		width: 50%;
		margin: 0 auto;
		.pldsectopi{
			cursor: pointer;
		    float: right;
		    width: 40px;
		    height: 20px;
		    background-color: #18d1ff;
		    border-radius: 300px;
		    overflow: hidden;
		    i{
		    	height: 18px;
			    width: 19px;
			    float: right;
			    border-radius: 50%;
			    background-color: #fff;
			    position: relative;
			    left: -1px;
			    top: 1px;
		    }
		}
		.pldpp{
			overflow: hidden;
			.pldsectopi,span{
				float: left;
			}
		}	
	}
</style>


