<template>
	<div class="pldequipment container-fluid">
		<ul>
			<li v-for="(it,index) in temperature.namelists">
				<span class="pldleft">
					<img src="static/img/sanjiao.png" alt="sanjiao" v-show="it.pldshow"/>
					<span>
						{{it.pp}}
					</span>
				</span>
				<div v-if="index==0||index==2" class="pldlidiv pldvall">
					<!--<select>
						<option v-if="index==0" v-for="ita in temperature.optionlist[index]">{{ita.pp}}</option>
						<option v-if="index==2" v-for="itaa in temperature.optionlist[index-1]">{{itaa.pp}}</option>
					</select>
					<img src="static/img/bottom.png" alt="bottom"/>-->
				</div>
				<div v-else-if="index==1" class="pldvall">
					<input @input="pldsett" type="text" v-model="pldallsj" :placeholder="it.pla" />
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		name:'temperature',
		data(){
			return{
				pldallsj:'',
				temperature:{
					namelists:[
						{pp:'',pldshow:false},
						{pp:'修改名称：',pldshow:true,pla:'请输入要修改名称'}
					],
					optionlist:[
//						[
//							{pp:'主卧'},
//							{pp:'次卧'},
//							{pp:'客厅'},
//							{pp:'浴室'}
//						],
						[
							{pp:'设置报警阀值'},
							{pp:'设置报警阀值'},
							{pp:'设置报警阀值'},
							{pp:'设置报警阀值'}
						]
					]
				}
			}
		},
		methods:{
			'pldsett':function(){
				this.$root.eventHub.$emit('plddis')
			}
		},
		mounted:function(){
			let that=this
			this.$root.eventHub.$emit('plddismr')
			this.$root.eventHub.$on('pldfssj',function(){
				that.pld_global.pldiptval=that.pldallsj
			})
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	height: 400px;
	.pldvalldiv{
		img{
			position: relative;
			top: -6px;
			right: -2px;
		}
		padding-left:8%;
		color:#a9a9a9;
	}
	ul{
		li{
			overflow: hidden;
			margin-bottom: 20px;
			.pldleft{
				display: block;
				width: 7%;
				text-align: right;
				float: left;
				margin-right:10px;
				line-height:40px;
				img{
					position: relative;
					top: -3px;
					right: -2px;
				}
			}
			.pldlidiv{
				position: relative;
				img{
					position: absolute;
					right: 30px;
					top: 16px;
				}
			}
			.pldvall{
				float: left;
				width: 90%;
			}
			input,select{
				border-radius: 6px;
				padding-left: 15px;
				width: 100%;
				height: 40px;
				border: none;
				border: 1px solid #dbdbdb;
				outline: none;
			}
			.pldipt{
				input{
					width: 49%;
				}
			}
		}
	}
}
</style>


