<template>
	<div class="pldall">
		<maskl style="z-index: 66;" v-show="pldshowmore" :masks="masks" :pldmaskcom='plderrorzj' :plddis='plddis' @pldcls='pldclose' :plddist='plddist'></maskl>
	</div>
</template>

<script>
  import maskl from '@/components/common_model/masklayer'
  export default{
  	name:'errortc',
  	data(){
  		return{
  			pldroom:'',
  			pldtoken:'',
  			pldwsfin:'',
  			pldshowmore:false,
  			masks:[
				[
					{title:'实时告警事件'}
				],
				[
					{title:'控制设备',cla:'pldcla',pldfunc:this.pldchangebtn},
					{title:'安防设备',pldfunc:this.pldchangebtn},
					{title:'监测设备',pldfunc:this.pldchangebtn}
				],
				[
					{title:'下一步',cla:'pldclahide',pldfunc:this.pldcloseall,dd:true},
					{title:'取消',cla:'pldclahide',pldfunc:this.pldchangebtn},
				],
				[
					{title:false}
				]
			],
			plderrorzj:{
				data(){
					return{
						pldtable:[
							{pp:'描述',ww:'40%'},
							{pp:'时间',ww:'20%'},
							{pp:'地点',ww:'20%'},
							{pp:'操作',ww:'20%'},
						],
						pldtabletb:[],
					}
				},
				template:`<table class='pldtable' style="width:100%;text-align:center;font-Size:16px;display:block;height: 510px;overflow: auto;">
					<thead style="width:100%;display:block;">
						<tr style="display:block;width:100%;overflow:hidden;font-weight:bold">
							<td v-for="it in pldtable"  :style="{color: '#333',fontSize: '16px',width:it.ww,paddingTop:'8px',paddingBottom:'30px',display:'block',float:'left'}">
								{{it.pp}}
							</td>
						</tr>
					</thead>
					<tbody style="width:100%;display:block;">
						<tr v-for="ia in pldtabletb"  style="color:red;border-top:1px solid #eeeeee;padding:25px 0 40px 0;display:block;width:100%">
							<td  style="width:40%;display:block;float:left">
							<img src="static/img/errorimg.png" alt='解除' style="position:relative;top:-4px;left:-36px"/>
								{{ia.pp}}
							</td>
							<td style="width:20%;display:block;float:left">
								{{ia.time}}
							</td>
							<td style="width:20%;display:block;float:left">
								{{ia.add}}
							</td>
							<td style="width:20%;display:block;float:left">
								<button style="cursor: pointer;width: 70px;height: 36px;border:none;margin-top:-8px;background: -webkit-linear-gradient(left, #8652ff, #1bd1f5);color: #fff;text-align: center;border-radius: 300px;line-height: 36px;">
									{{ia.clk}}
								</button>
							</td>
						</tr>
					</tbody>
				</table>`,
			  	mounted:function(){
			  		
					let that=this
					//公共websocket方法
					let ee
					this.$root.eventHub.$on('pldgetglobal',function(){
						
						if(that.$route.path=='/'){
							return
						}else{
							ee=new WebSocket('ws://'+that.pld_global.globalwsurl+'/ws?maxiiot_user='+window.localStorage.getItem('token')+'&roomID='+that.pld_global.roomid)
						}
					})
					this.$root.eventHub.$emit('pldgetglobal')
					
					this.$root.eventHub.$on('pldglometh',function(){
						ee=new WebSocket('ws://'+that.pld_global.globalwsurl+'/ws?maxiiot_user='+window.localStorage.getItem('token')+'&roomID='+this.pld_global.roomid)
						ee.onopen=function(){
							console.log('socket已经连接')
						}
						
						ee.onmessage=function(evt){
							var l = eval("(" + evt.data + ")");
							switch (l.types){
								case 1:
									that.$root.eventHub.$emit('pldchangezt1',l.id)
									break;
								case 2:
									let pldfortime=that.pld_global.dateFormat(l.times)
									let pldsj={pp:l.data[2],time:pldfortime,add:l.data[1],clk:'解除'}
									that.pldtabletb.splice(0,0,pldsj)
									that.$root.eventHub.$emit('plderrorshow')
									break;
								case 3:
									that.$root.eventHub.$emit('pldchangezt',l.id,l.data)
									break
								default:
									break;
							}
							console.log(l)
						}
						
						ee.onclose=function(){
							console.log('socket已经关闭')
						}
					})
					
					
					this.$root.eventHub.$on('pldwsclose',function(){
//						ws=new WebSocket('ws://192.168.181.60:8999/ws?maxiiot_user='+tk+'&roomID='+ee)
						ee.onclose=function(){
							console.log('socket已经关闭')
						}
						ee.close()
					})
					
			  	}
			},
			plddis:'',
			plddist:''
  		}
  	},
  	components:{
  		maskl
  	},
  	methods:{
  		pldclose:function(){
  			this.pld_global.pldshow=false
  			this.pldshowmore=false
  		}
  	},
  	mounted:function(){
  		let that=this
  		that.$root.eventHub.$on('plderrorshow',function(){
  			that.pldshowmore=true
  		})
  	}
  }
</script>

<style lang="less" scoped="scoped">
</style>



