<template>
	<div class="pldtopall container-fluid">
		<div class="pldtopleft">
			<ul v-if="pldtop[0].kg">
				<li v-for="(it,index) in pp">
					<a :class="it.cla" :data_id='it.id' :data_roomnumid='it.roomnumid' @click="pldgeta($event,index,it.title)">
						{{it.title}}
					</a>
				</li>
			</ul>
			<ul v-else>
				<li>
					<p>
						{{pldtop[2].pp}}
					</p>
				</li>
			</ul>
		</div>
		<div class="pldtopright">
			<a class="pldtopa" @click="pldshow"><img src="static/img/add.png" alt="add">{{pptext}}</a>
			
			<div class="pldmessagemath" @click="pldtogglediv">
				<img src="static/img/message_top.png" alt="message_top"/>
				<div class="pldcursor">
					<span>
						{{datamath}}
					</span>
				</div>
				
				<!--<div class="pldmessageall">
					<div class="pldmessagetopone">
						<span>
							{{pldmessageyh[0].pp}}
						</span>
						<a href="#/mess">
							{{pldmessageyh[1].pp}}
						</a>
					</div>
					<div class="pldmessageli">
						<ul>
							<li v-for="it in pldmessageli" @click="pldchangelink" :data_idmore='it.id'>
								<span :data_idmore='it.id'>{{it.pp}}</span>
								<img :data_idmore='it.id' class="pldimgfin" src="static/img/message_dian_03.gif" alt="dian"/>
							</li>
						</ul>
					</div>
				</div>-->
				
			</div>
			
			<div class="pldtopimg">
				<img src="static/img/topimg.png" alt="topimg"/>
				<span>
					{{name}}
				</span>
				<a href="/login">Exit</a>
			</div>
			
			
		</div>
	</div>
</template>

<script>
	export default{
		name:'top',
		props:['pldtop'],
		data(){
			return{
				pp:[
					{title:'主卧',linker:'/main',cla:'pldmorea'},
					{title:'次卧',linker:'/main',cla:''},
					{title:'客厅',linker:'/main',cla:''},
					{title:'厨房',linker:'/main',cla:''},
					{title:'浴室',linker:'/main',cla:''}
				],
				pptext:'添加设备',
				datamath:10,
				pldmessageyh:[
					{pp:'消息中心'},
					{pp:'查看全部'}
				],
				name:'devin',
	 	  		pldmessageli:[
	 	  			{pp:'test52252151515415156162512651',isready:true,id:'21563542'},
	 	  			{pp:'test',isready:true,id:'21563542'},
	 	  			{pp:'test',isready:true,id:'21563542'}
	 	  		]
			}
		},
		methods:{
			'pldchangelink':function(e){
	 	  		window.localStorage.setItem('pldmyiderror',$(e.target).attr('data_idmore'))
	 	  		window.location.reload()
//	 	  		window.location.href='#/message'
	 	  	},
			'pldgeta':function(e,data,tt){
				this.pld_global.pldclkmath++
				let that=this
				for(let i=0;i<this.pp.length;i++){
					that.pp[i].cla=''
				}
				this.pp[data].cla='pldmorea'
				this.pld_global.title=tt
				this.pld_global.roomid=this.pp[data].id
				this.$emit('getsevice',this.pld_global.roomid,this.pld_global.pldsevice)
				this.$root.eventHub.$emit('pldbdroom',this.pld_global.roomid)
//				let ws=new WebSocket('ws://192.168.181.60:8999/ws?maxiiot_user='+window.localStorage.getItem('token')+'&roomID='+this.pld_global.roomid)
				this.$root.eventHub.$emit('pldwsclose')
				this.$root.eventHub.$emit('pldglometh')
			},
	 	  	'pldtogglediv':function(e){
	 	  		this.$emit('pldmath')
//	 	  		$('.pldmessageall').toggle()
//	 	  		$('.pldmessagemath').toggleClass('pldmoreclass')
	 	  	},
	 	  	'pldshow':function(){
	 	  		this.$emit('pldparent')
	 	  	},
	 	  	'pldroom':function(){
	 	  		let that=this
			
				//获取所有房间
				this.$ajax.get(this.pld_global.globalurl+'/room',{
		      		RoomNum:1,
			      	headers:{
			      		'Accept': 'application/json',
			      		"maxiiot_user" :this.pld_global.logincookie
			      	}
			    })
			      .then((res)=>{
			      	this.pp.splice(0,this.pp.length)
			      	for(let i=0;i<res.data.Data.length;i++){
			      		let pldlist={title:'',id:'',roomnumid:'',cla:''}
			      		pldlist.title=res.data.Data[i].Name
			      		pldlist.id=res.data.Data[i].id
			      		pldlist.roomnumid=res.data.Data[i].RoomNumID
			      		if(i==0){
			      			pldlist.cla='pldmorea'
			      		}
			      		this.pp.push(pldlist)
			      	}
			      	that.$emit('getsevice',res.data.Data[0].id,that.pld_global.pldsevice)
			      	that.pld_global.roomid=res.data.Data[0].id
			      	console.log('top')
			      })
			      .catch((error)=>{
			      	
			      })
			      
	 	  	}
		},
		mounted:function(){
			let that=this
			this.$root.eventHub.$on('pldroom',function(){
				that.pldroom()
			})
			this.pldroom()
			
			this.$root.eventHub.$on('pldgetroom',function(){
				that.pldroom()
			})
			
//			$(document).mouseup(function(e){
//			  var _con = $('.pldmessagemath');   // 设置目标区域
//			  if(!_con.is(e.target) && _con.has(e.target).length === 0){ // Mark 1
//			  	$('.pldmessageall').hide()
//			  	$('.pldmessagemath').removeClass('pldmoreclass')
//			  }
//			});
			
			
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldtopall{
	a{
		color: #919495;
	}
	position: absolute;
	top: 0;
	padding-right: 0;
	width: 100%;
	padding-left: 215px;
	height: 95px;
	padding-top: 30px;
	padding-bottom: 30px;
	/*overflow: hidden;*/
	.pldtopleft{
		width:50%;
		float:left;
		ul{
			overflow: hidden;
			li{
				float: left;
				padding-right: 40px;
				overflow: hidden;
				p{
					color: #000;
					font-size: 20px;
				}
				a{
					cursor: pointer;
					width: 80px;
					height: 37px;
					border-radius: 300px;
					/*background: -webkit-linear-gradient(left,#8652ff,#1bd1f5);*/
					display: inline-block;
					overflow: hidden;
					line-height: 37px;
					text-align: center;
				}
				.pldmorea{
					background: -webkit-linear-gradient(left,#8652ff,#1bd1f5);
					color: #fff;
				}
			}
		}
	}
	.pldtopright{
		margin-top: 3px;
		width: 450px;
		padding-right: 25px;
		float: right;
		position: relative;
		.pldtopa{
			cursor: pointer;
			width: 123px;
			height: 36px;
			float: left;
			background: -webkit-linear-gradient(left,#8652ff,#1bd1f5);
			color: #fff;
			text-align: center;
			border-radius: 300px;
			line-height: 36px;
			img{
				margin-right: 5px;
				position: relative;
				top: -2px;
			}
		}
		.pldmessagemath{
			margin-left: 50px;
			margin-right: 30px;
			cursor: pointer;
		    float: left;
		    width: 66px;
		    height: 56px;
		    position: relative;
		    top: -14px;
		    /*background-color: #ccc;*/
		    img{
		    	position: relative;
			    top: 17px;
			    left: 21px;
		    }
			.pldcursor{
				width: 35px;
			    height: 23px;
			    background-color: #fff;
			    border-radius: 300px;
			    position: relative;
			    top: -13px;
			    left: 38px;
			    z-index: 10;
				span{
					line-height: 24px;
					color: #fff;
				    position: relative;
				    z-index: 12;
				    text-align: center;
				    display: block;
				    font-size: 14px;
				}
			}
			.pldcursor:after{
				content: '';
			    display: block;
			    position: absolute;
			    top: 2px;
			    left: 2px;
			    width: 31px;
			    height: 20px;
			    border-radius: 300px;
			    background-color: #f65e5e;
			    z-index: 5;
			}
			.pldmessageall{
				z-index: 10;
				display: none;
				background-color: #fff;
				border-radius: 6px;
				width: 300px;
				position:absolute;
				top: 56px;
				left: -233px;
				color:#000;
				box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.1);
				.pldmessagetopone{
					padding: 20px;
					overflow: hidden;
					border-bottom: 1px solid #e5e5e5;
					position:relative;
					span{
						float: left;
					}
					a{
						color:#000;
						float: right;
						font-size: 14px;
						width: 100%;
					    position: absolute;
					    right: 0;
					    text-align: right;
					    padding-right: 20px;
					    padding-top: 22px;
					    height: 100%;
					    top: 0;
					}
				}
				.pldmessageli{
					padding: 20px;
					overflow: hidden;
					ul{
						li{
							overflow: hidden;
							span{
								overflow: hidden;
								width: 235px;
								float: left;
								white-space: nowrap;
								word-break: break-all;
								text-overflow: ellipsis;
								font-size: 16px;
								letter-spacing: 3px;
								color: #333;
								line-height: 30px;
							}
							img{
								position: relative;
								top: 5px;
								float: right;
							}
							.pldimgfin{
								position: relative;
								top: 0;
								float: none;
								left: 8px;
							}
						}
					}
				}
			}
		}
		.pldtopimg{
			img{
				margin-right: 6px;
			}
			span{
				margin-right: 20px;
			}
			a{
				color: #0099ff;
				font-size: 17px;
			}
		}
		.pldmessagemath:hover{
			background-color: #0e9eb8;
		}
		
	}
}
</style>


