<template>
	<div class="pldequipment container-fluid">
		<div class="peopleleft">
			<div v-if="pldaskupdate[0].pldkg==true" class="peopleimg">
				<img src="static/img/hongwai.png" alt="people"/>
				<p>
					{{peoplezt}}
				</p>
			</div>
			<div v-if="pldaskupdate[0].pldkg==false" class="peopleimg">
				<img src="static/img/ask.png" alt="ask"/>
				<p style="color: red;">
				 	{{pldnonesj}}
				</p>
			</div>
			<!--<div v-for="(it,index) in peoplelist" v-if="index==2" class="peoplexxnone">
				<p>
					{{it.pp}}
				</p>
				<span>
					{{it.smallp}}
				</span>
			</div>-->
			<div v-for="(it,index) in peoplelist" class="peoplexx">
				<p>
					{{it.pp}}
				</p>
				<!--<span v-if="pldaskupdate[1].pldkg==true&&index==0">
					{{it.smallp}}
				</span>
				<span v-if="pldaskupdate[1].pldkg==false&&index==0">
					{{pldnonesb}}
				</span>-->
				<span>
					{{it.smallp}}
				</span>
			</div>
		</div>
		<div class="peopleright" v-show="pldzx[0].pldkg==true">
			<div class="zxo" id="zxo" style="color: red;">
				{{pldpp}}
			</div>
			<div class="zxt" id="zxt" style="color: red;">
				{{pldpp}}
			</div>
		</div>
		<div class="peopleright" v-show="pldzx[0].pldkg==false">
			<div class="pldnonesj" v-for="(itta,ii) in pldszdata">
				<p class="pldnonesjp">
					{{itta.title}}
				</p>
				<p>
					{{itta.pp}}
				</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'peopledate',
		data(){
			return{
				pldszdata:[
					{title:'心率',pp:'暂无心率设备，请添加或关联设备'},
					{title:'呼吸',pp:'暂无心率设备，请添加或关联设备'}
				],
				pldzx:[
					{pldkg:true}
				],
				pldnonesb:'-',
				pldaskupdate:[
					{pldkg:false},
					{pldkg:false}
				],
				pldnonesj:'暂无数据，请添加或关联设备',
				peoplezt:'安静状态',
				peoplelist:[
					{
						pp:'已关联设备：',
						smallp:'主卧的人体感应器'
					},
					{
						pp:'所在位置：',
						smallp:'主卧的人体感应器'
					},
					{
//						pp:'已关联设备：',
//						smallp:'主卧的人体感应器'
					}
				],
				pldpp:'暂无数据，请添加或关联设备',
				pldname:'',
				pldtime:[],
				pldser:[],
				plddata:[],
				pldname1:'',
				pldtime1:[],
				pldser1:[],
				plddata1:[],
				pldtest:'',
				pldtest1:'',
				pldtestt:[],
				pldtestt1:[],
				pldsj:0
			}
		},
		mounted:function(){
			
			let that=this
			
			that.$root.eventHub.$on('pldchangenei',function(dd){
				that.pldnonesj=dd
			})
			
//				this.$ajax.get(this.pld_global.globalurl+'/room/devicesData',{
//					params:{
//						deviceID:that.pld_global.deviceid
//					},
//			      	headers:{
//			      		'Accept': 'application/json',
//			      		"maxiiot_user" :this.pld_global.logincookie
//			      	}
//				})
//				.then(function(res){
//					if(res.data.Data.length>0){
//						that.pldzx[0].pldkg=true
//						that.$root.eventHub.$emit('pldgetid',res.data.Data[1].ID)
//						that.peoplelist[0].smallp=that.pld_global.pldnamemore
//						that.peoplelist[1].smallp=that.pld_global.title
//					}
//				})
//				.catch(function(){
//					
//				})
			
			
			// 引入 ECharts 主模块
			var echarts = require('echarts/lib/echarts');
			// 引入柱状图
			require('echarts/lib/chart/line');
			// 引入提示框和标题组件
			require('echarts/lib/component/tooltip');
			require('echarts/lib/component/title');
//			require('echarts/lib/component/legend');
			
			// 基于准备好的dom，初始化echarts实例
			var myChart = echarts.init(document.getElementById('zxt'));
			// 绘制图表
			
			let pldoption={
				title: {
			        text: '实时数据',
			        subtext:that.pldname,
			        x:'50',
        			y:'0',
        			textStyle: {
					    lineHeight: 26,
					    color:'#333333',
					    show:true,
					    height:120,
					    fontSize:16,
					    rich: {
				    	 height:120,
					      a: {
					        lineHeight: 56,
					        height:120
					      }
					    }
					  }
			    },
			    tooltip: {
			        trigger: 'axis'
			    },
			    legend: {
			        data:that.plddata,
			        top:'50'
			    },
			    grid: {
			    	top:'20%',
			        left: '3%',
			        right: '4%',
			        bottom: '20%',
			        containLabel: true
			    },
			    toolbox: {
			        feature: {
			            saveAsImage: {}
			        }
			    },
			    xAxis: {
			        type: 'category',
			        boundaryGap: false,
			        data: that.pldtime
			    },
			    yAxis: {
			        type: 'value'
			    },
			    series: that.pldser
			}
			
			// 基于准备好的dom，初始化echarts实例
			var myChart1 = echarts.init(document.getElementById('zxo'));
			// 绘制图表
			
			let pldoption1={
				title: {
			        text: '实时数据',
			        subtext:that.pldtest1,
			        x:'50',
        			y:'0',
        			textStyle: {
					    lineHeight: 26,
					    color:'#333333',
					    show:true,
					    height:120,
					    fontSize:16,
					    rich: {
				    	 height:120,
					      a: {
					        lineHeight: 56,
					        height:120
					      }
					    }
					  }
			    },
			    tooltip: {
			        trigger: 'axis'
			    },
			    legend: {
			        data:that.pldtestt1,
			        top:'50'
			    },
			    grid: {
			    	top:'20%',
			        left: '3%',
			        right: '4%',
			        bottom: '20%',
			        containLabel: true
			    },
			    toolbox: {
			        feature: {
			            saveAsImage: {}
			        }
			    },
			    xAxis: {
			        type: 'category',
			        boundaryGap: false,
			        data: that.pldtime1
			    },
			    yAxis: {
			        type: 'value'
			    },
			    series: that.pldser1
			}
			
			if(that.pldsj>0){
				that.$root.eventHub.$on('pldchangezt1',function(){
					pldccaa()
				})
			}
			
			function pldccaa(){
				//获取数据
			switch (that.pld_global.agreementid){
				case 2:
						that.$ajax.get(that.pld_global.globalurl+'/room/devicesData',{
							params:{
								limit:13,
								offset:0,
								deviceID:that.pld_global.deviceid
								//that.pld_global.deviceid
							},
					      	headers:{
					      		'Accept': 'application/json',
					      		"maxiiot_user" :that.pld_global.logincookie
					      	}
						})
						.then(function(res){
							that.pldsj=res.data.Data.length
							if(res.data.Data.length>1){
								that.pldzx[0].pldkg=true
								that.$root.eventHub.$emit('pldgetid',res.data.Data[1].ID)
								that.peoplelist[0].smallp=that.pld_global.pldnamemore
								that.peoplelist[1].smallp=that.pld_global.title
								that.pldnonesj=''
								canavs1(res)
								canavs2(res)
								that.pldpp=''
							}else if(res.data.Data.length==1){
								that.pldzx[0].pldkg=false
								canavs2(res)
								that.peoplelist[0].smallp='-'
								that.peoplelist[1].smallp='-'
								that.pldpp='暂无数据，请添加或关联设备'
							}
							
//							pldoption.title.subtext=res.data.Data[0].DataType[0]+res.data.Data[0].DataType[1]+res.data.Data[0].DataType[2]+res.data.Data[0].DataType[3]
//							pldoption.legend.data=[res.data.Data[0].DataType[0],res.data.Data[0].DataType[1],res.data.Data[0].DataType[2],res.data.Data[0].DataType[3]]
							//绘制第一张图
							function canavs1(res){
								for(let im=0;im<res.data.Data[0].DataType.length;im++){
									that.pldtest+=res.data.Data[0].DataType[im]
									that.pldtestt.push(res.data.Data[0].DataType[im])
								}
								pldoption.title.subtext=that.pldtest
								pldoption.legend.data=that.pldtestt
								for(let i=0;i<res.data.Data[0].Time.length;i++){
									that.pldtime.push(that.pld_global.dateFormat(res.data.Data[0].Time[i]))
								}
								console.log(that.pldtime)
								for(let ii=0;ii<res.data.Data[0].Data.length;ii++){
									let plddx={
							            name:'',
							            type:'line',
							            stack: '总量',
							            data:[]
							        }
									plddx.name=res.data.Data[0].DataType[ii]
									for(let it=0;it<res.data.Data[0].Data[ii].length;it++){
										plddx.data.push(res.data.Data[0].Data[ii][it])
									}
									that.pldser.push(plddx)
								}
								console.log(that.pldser)
								
								
								myChart.setOption(pldoption,true);//true重绘
							}
							
							//绘制第二张图
							function canavs2(res){
								for(let im=0;im<res.data.Data[1].DataType.length;im++){
									that.pldtest1+=res.data.Data[1].DataType[im]
									that.pldtestt1.push(res.data.Data[1].DataType[im])
								}
								pldoption1.title.subtext=that.pldtest1
								pldoption1.legend.data=that.pldtestt1
								for(let i=0;i<res.data.Data[1].Time.length;i++){
									that.pldtime1.push(that.pld_global.dateFormat(res.data.Data[1].Time[i]))
								}
								console.log(that.pldtime1)
								for(let ii=0;ii<res.data.Data[1].Data.length;ii++){
									let plddx={
							            name:'',
							            type:'line',
							            stack: '总量',
							            data:[]
							        }
									plddx.name=res.data.Data[1].DataType[ii]
									for(let it=0;it<res.data.Data[1].Data[ii].length;it++){
										plddx.data.push(res.data.Data[1].Data[ii][it])
									}
									that.pldser1.push(plddx)
								}
								console.log(that.pldser1)
								
								
								myChart1.setOption(pldoption1,true);//true重绘
							}
							
//							pldoption1.title.subtext=res.data.Data[1].DataType[0]+res.data.Data[1].DataType[1]+res.data.Data[1].DataType[2]+res.data.Data[1].DataType[3]
//							pldoption1.legend.data=[res.data.Data[1].DataType[0],res.data.Data[1].DataType[1],res.data.Data[1].DataType[2],res.data.Data[1].DataType[3]]
							
						})
						.catch(function(){
							
						})
					break;
				default:
					break;
			}
			}
			pldccaa()
			
			
//			//获取数据
//			switch (that.pld_global.agreementid){
//				case 23:
//						this.$ajax.get(that.pld_global.globalurl+'/room/devicesData',{
//							params:{
//								limit:13,
//								offset:0,
//								deviceID:74
//								//that.pld_global.deviceid
//							},
//					      	headers:{
//					      		'Accept': 'application/json',
//					      		"maxiiot_user" :that.pld_global.logincookie
//					      	}
//						})
//						.then(function(res){
//							
//						})
//						.catch(function(){
//							
//						})
//					break;
//				default:
//					break;
//			}
			
			
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	height: 500px;
	overflow: hidden;
	.peopleleft{
		float: left;
		font-size: 16px;
		width: 30%;
		border-right: 1px solid #dddddd;
		.peopleimg{
			color: #18d1ff;
			border-bottom: 1px solid #dddddd;
			padding-top:8px;
			p{
				font-size: 14px;
				margin-top: 20px;
				margin-bottom: 20px;
			}
			text-align:center;
		}
		.peoplexx{
			padding-left: 10px;
			border-bottom:1px solid #dddddd;
			padding-top: 25px;
			padding-bottom: 35px;
			p{
				color: #333333;
				margin-bottom: 10px;
			}
			span{
				color: #747474;
			}
		}
		.peoplexxnone{
			border: none;
			padding-left: 10px;
			padding-top: 25px;
			padding-bottom: 25px;
			p{
				color: #333333;
				margin-bottom: 10px;
			}
			span{
				color: #747474;
			}
		}
	}
	.peopleright{
		float: right;
		width: 70%;
		.pldnonesj{
			height: 220px;
			padding-left: 25px;
			padding-top: 10px;
			.pldnonesjp{
				color: #333;
				font-size: 18px;
				text-align: left;
				margin-top: 0;
			}
			p{
				color: red;
				text-align: center;
				margin-top: 35px;
			}
		}
		.zxo,.zxt{
			width: 100%;
			height: 220px;
		}
		.zxt{
			margin-top: 30px;
		}
	}
}
</style>


