import Vue from 'vue'
import Router from 'vue-router'
import left from '@/components/common_model/left'
import sevice from '@/components/common_model/sevice'
import index from '@/components/index'
import top from '@/components/common_model/top'
import mains from '@/components/page/index'
import bedroom from '@/components/common_model/bedroom'
import af from '@/components/page/af'
import jc from '@/components/page/jc'
import maskl from '@/components/common_model/masklayer'
import allequipment from '@/components/equipment/allequipment'
import add from '@/components/equipment/addequipment'
import equipment from '@/components/common_model/equipment'
import deletes from '@/components/common_model/delete'
import temperature from '@/components/common_model/temperature'
import allsetting from '@/components/common_model/allsetting'
import commonsetting from '@/components/common_model/commonsetting'
import updatadate from '@/components/common_model/updatadate'
import agodate from '@/components/common_model/agodate'
import fenye from '@/components/common_model/fenye'
import errormessage from '@/components/common_model/errormessage'
import peopledate from '@/components/common_model/peopledate'
import test from '@/components/page/test'
import errortc from '@/components/common_model/errortc'
import smartSwitch from '@/components/sevice_setting/smart_switch'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/left',
      name: 'left',
      component: left
    },
    {
      path: '/peopledate',
      name: 'peopledate',
      component: peopledate
    },
    {
      path: '/smartSwitch',
      name: 'smartSwitch',
      component: smartSwitch
    },
    {
      path: '/errortc',
      name: 'errortc',
      component: errortc
    },
    {
      path: '/errormessage',
      name: 'errormessage',
      component: errormessage
    },
    {
      path: '/sevice',
      name: 'sevice',
      component: sevice
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/',
      name: 'login',
      component: test
    },
    {
      path: '/allsetting',
      name: 'allsetting',
      component: allsetting
    },
    {
      path: '/commonsetting',
      name: 'commonsetting',
      component: commonsetting
    },
    {
      path: '/top',
      name: 'top',
      component: top
    },
    {
      path: '/bedroom',
      name: 'bedroom',
      component: bedroom
    },
    {
      path: '/mains',
      name: 'mains',
      component: mains
    },
    {
      path: '/fenye',
      name: 'fenye',
      component: fenye
    },
    {
      path: '/af',
      name: 'af',
      component: af
    },
    {
      path: '/jc',
      name: 'jc',
      component: jc
    },
    {
      path: '/maskl',
      name: 'maskl',
      component: maskl
    },
    {
      path: '/agodate',
      name: 'agodate',
      component: agodate
    },
    {
      path: '/equipment',
      name: 'equipment',
      component: equipment
    },
    {
      path: '/allequipment',
      name: 'allequipment',
      component: allequipment
    },
    {
      path: '/temperature',
      name: 'temperature',
      component: temperature
    },
    {
      path: '/deletes',
      name: 'deletes',
      component: deletes
    },
    ,
    {
      path: '/updatadate',
      name: 'updatadate',
      component: updatadate
    },
    {
      path: '/add',
      name: 'add',
      component: add
    }
  ]
})



