<template>
<div style="height: 100%;">
	<left @getsevice="pldsevice" @pldreturn='pldleft'></left>
	<top @pldmath='pldpamath' :pldtop='plderror' @pldparent="pldxstc" @getsevice="pldsevice"></top>
	<bedroom v-if="plderror[0].kg"></bedroom>
	<div v-if="plderror[0].kg" class="plddiv container-fluid">
		<sevice v-for="(it,index) in pldlist" :pldsevlist='index' :pldallsj='pldlist' @pldrealli='it.pldrealli' :sevicelist='pldlist' :pldmaths='index' @pldsevclk='pldsevclk' :key="index" class="col-md-3 col-xs-3 col-sm-3" :title="it.test" :kgtop="it.kgtop" @test='myfunc' :licoloro="it.licoloro" :licolort="it.licolort" :litexto="it.litexto" :litextt="it.litextt"></sevice>
	</div>
	<errormessage v-if="plderror[1].kg"></errormessage>
	<equipment :pldqm="pldcloseall" :plddisqm='plddis' :plddisqmt='plddismr' :pldqmo="pldfangfa" :pldupdate='pldupdateo' :pldupdatesj='pldtestfc' :pldfc="pldtestall" v-show="tcshow" :finsj="masks" :pldcom="pldequipcom"></equipment>
	<div class="pldtext" v-show="textshow">
		<p>
			{{pldtext}}
		</p>
	</div>
</div>
</template>

<script>
	import left from '@/components/common_model/left'
	import top from '@/components/common_model/top'
	import sevice from '@/components/common_model/sevice'
	import bedroom from '@/components/common_model/bedroom'
	import equipment from '@/components/common_model/equipment'
	import allequipment0 from '@/components/equipment/allequipment'
	import allequipment1 from '@/components/equipment/afequipment'
	import allequipment2 from '@/components/equipment/jcequipment'
	import add from '@/components/equipment/addequipment'
	import deletes from '@/components/common_model/delete'
	import temperature0 from '@/components/common_model/updatadate'
	import temperature1 from '@/components/common_model/agodate'
	import temperature2 from '@/components/common_model/temperature'
	import allsetting0 from '@/components/common_model/peopledate'
	import allsetting1 from '@/components/common_model/allsetting'
	import allsetting2 from '@/components/common_model/commonsetting'
	import errormessage from '@/components/common_model/errormessage'
	export default{
		name:'mains',
		components:{
			left,
			bedroom,
			top,
			sevice,
			equipment,
			allequipment0,
			allequipment1,
			allequipment2,
			add,
			deletes,
			temperature0,
			temperature1,
			temperature2,
			allsetting1,
			allsetting2,
			allsetting0,
			errormessage
		},
		data(){
			return{
				pldbj:0,
				plderror:[
					{kg:true},
					{kg:false},
					{pp:'告警消息中心'}
				],
				pldtestfc:'',
				textshow:false,
				pldtext:'添加成功！',
				pldmorefc:'',
				plddata:0,
				pldtestall:{
					cko:this.pldtesto,
					ckt:this.pldtestt
				},
				pldtestallo:{
					cko:this.pldfin,
					ckt:this.pldprev
				},
				tcshow:false,
				pldequipcom:deletes,
				plddeletesj:0,
				//获取设备id
				pldgetid:0,
				plddelete:[
						[
							{title:'人体特征感知床垫'}
						],
						[
							{title:'控制设备',cla:'pldcla',pldfunc:this.pldchangebtn},
							{title:'安防设备',pldfunc:this.pldchangebtn},
							{title:'监测设备',pldfunc:this.pldchangebtn}
						],
						[
							{title:'确定',cla:'pldcla',pldfunc:this.plddeletereal},
							{title:'取消',pldfunc:this.pldcloseall}
						],
						[
							{title:false}
						]
					],
				pldtemperature:[
							[
								{title:'主卧的温湿度计'}
							],
							[
								{title:'实时数据',cla:'pldcla',pldfunc:this.pldchangebtno},
								{title:'历史数据',pldfunc:this.pldchangebtno},
								{title:'设置',pldfunc:this.pldchangebtno}
							],
							[
								{title:'保存',cla:'pldcla',pldfunc:this.pldgive,dd:true},
//								{title:'更新时间',pldfunc:this.updatetime},
								{title:'取消',pldfunc:this.pldcloseall}
							],
							[
								{title:true}
							]
						],
				pldupdatadate:[
					[
						{title:'主卧的温湿度计'}
					],
					[
						{title:'实时数据',cla:'pldcla',pldfunc:this.pldchangebtno},
						{title:'历史数据',pldfunc:this.pldchangebtno},
						{title:'设置',pldfunc:this.pldchangebtno}
					],
					[
						{title:'保存',cla:'pldhide'},
						{title:'更新时间',cla:'pldhide'},
						{title:'取消',cla:'pldhide'}
					],
					[
						{title:true}
					]
				],
				pldupdatadate2:[
					[
						{title:'主卧的温湿度计'}
					],
					[
						{title:'历史数据',cla:'pldcla',pldfunc:this.pldchangebtno22},
						{title:'设置',pldfunc:this.pldchangebtno22}
					],
					[
						{title:'保存',cla:'pldhide'},
						{title:'更新时间',cla:'pldhide'},
						{title:'取消',cla:'pldhide'}
					],
					[
						{title:true}
					]
				],
				pldupdatadate3:[
					[
						{title:'主卧的温湿度计'}
					],
					[
						{title:'历史数据',cla:'pldcla',pldfunc:this.pldchangebtno22},
						{title:'设置',pldfunc:this.pldchangebtno22}
					],
					[
						{title:'保存',cla:'pldcla',pldfunc:this.baocun,dd:true},
						{title:'取消',cla:'',pldfunc:this.pldcloseall}
					],
					[
						{title:true}
					]
				],
				pldupdatadate1:[
					[
						{title:'主卧的温湿度计'}
					],
					[
						{title:'设置',cla:'pldcla'}
					],
					[
						{title:'保存',cla:'pldcla',pldfunc:this.pldgive,dd:true},
						{title:'取消',pldfunc:this.pldcloseall}
					],
					[
						{title:true}
					]
				],
				peopledate:[
					[
						{title:'人体特征感知床垫'}
					],
					[
						{title:'数据',cla:'pldcla',pldfunc:this.pldchangebtnpeople},
						{title:'设置',pldfunc:this.pldchangebtnpeople}
					],
					[
						{title:'保存',cla:'pldhide'},
						{title:'取消',cla:'pldhide',pldfunc:this.pldcloseall}
					],
					[
						{title:true}
					]
				],
				peopledatet:[
					[
						{title:'人体特征感知床垫'}
					],
					[
						{title:'数据',pldfunc:this.pldchangebtnpeople},
						{title:'设置',cla:'pldcla',pldfunc:this.pldchangebtnpeople}
					],
					[
						{title:'保存',cla:'pldcla',dd:true,pldfunc:this.pldgive1},
						{title:'取消',pldfunc:this.pldcloseall}
					],
					[
						{title:true}
					]
				],
				privatemasks:[
						[
							{title:'添加设备'}
						],
						[
							{title:'控制设备',cla:'pldcla',pldfunc:this.pldchangebtn},
							{title:'安防设备',pldfunc:this.pldchangebtn},
							{title:'监测设备',pldfunc:this.pldchangebtn}
						],
						[
							{title:'下一步',cla:'pldcla',pldfunc:this.pldtesto,dd:true},
							{title:'取消',pldfunc:this.pldcloseall}
						],
						[
							{title:true}
						]
					],
				privatepldtestall:{
						cko:this.pldtesto,
						ckt:this.pldtestt
					},
				masks:[
					[
						{title:'添加设备'}
					],
					[
						{title:'控制设备',cla:'pldcla',pldfunc:this.pldchangebtn},
						{title:'安防设备',pldfunc:this.pldchangebtn},
						{title:'监测设备',pldfunc:this.pldchangebtn}
					],
					[
						{title:'下一步',cla:'pldcla',pldfunc:this.pldcloseall,dd:true},
						{title:'取消',pldfunc:this.pldchangebtn},
					],
					[
						{title:true}
					]
				],
				maskso:[
					[
						{title:'添加设备'}
					],
					[
						{title:'控制设备',cla:'pldcla',pldfunc:this.pldchangebtn},
						{title:'安防设备',pldfunc:this.pldchangebtn},
						{title:'监测设备',pldfunc:this.pldchangebtn}
					],
					[
						{title:'完成',cla:'pldcla',pldfunc:this.pldfin,dd:true},
						{title:'上一步',pldfunc:this.pldprev}
					],
					[
						{title:false}
					]
				],
				pldlist:[
					{
						test:'111',
						kgtop:true,
						licoloro:{
							color:'red',
							width:'40%',
							opacity:0
						},
						licolort:'#b8b8b8',
						litexto:'已经关闭',
						litextt:'已经关闭',
						show:false,
						pldrealli:this.pldrealli
					},
					{
						test:'1112',
						kgtop:true,
						licoloro:{
							color:'red',
							width:'40%',
							opacity:0
						},
						licolort:'#b8b8b8',
						litexto:'已经关闭',
						litextt:'已经关闭',
						show:false,
						pldrealli:this.testfucpeople
					},
					{
						test:'111',
						kgtop:true,
						licoloro:{
							color:'red',
							width:'40%',
							opacity:0
						},
						licolort:'#b8b8b8',
						litexto:'已经关闭',
						litextt:'已经关闭',
						show:false,
						pldrealli:this.testfuc
					},
					{
						test:'111',
						kgtop:true,
						licoloro:{
							color:'red',
							width:'40%',
							opacity:0
						},
						licolort:'#b8b8b8',
						litexto:'已经关闭',
						litextt:'已经关闭',
						show:false,
						pldrealli:this.testfuc
					},
					{
						test:'111',
						kgtop:true,
						licoloro:{
							color:'red',
							width:'40%',
							opacity:0
						},
						licolort:'#b8b8b8',
						litexto:'已经关闭',
						litextt:'已经关闭',
						show:false,
						pldrealli:this.testfuc
					},
					{
						test:'111',
						kgtop:true,
						licoloro:{
							color:'red',
							width:'40%',
							opacity:0
						},
						licolort:'#b8b8b8',
						litexto:'已经关闭',
						litextt:'已经关闭',
						show:false,
						pldrealli:this.testfuc
					}
				]
			}
		},
		methods:{
			//通用设置中保存
			'pldgive':function(){
				let that=this
				that.$root.eventHub.$emit('pldfssj')
				this.$ajax.put(this.pld_global.globalurl+'/room/devices',{
					agreementID:that.pldlist[that.pldgetid].pldagreementid,
					details:'',
					devName:that.pld_global.pldiptval,
					id:that.pldlist[that.pldgetid].id,
					roomID:that.pldlist[that.pldgetid].RoomID,
					status:true
				})
				.then(function(res){
					that.pldcloseall()
					that.pldlist[that.pldgetid].test=res.data.Data.DevName
				})
				.catch(function(){
					
				})
			},
			//智慧床垫设置中保存
			'pldgive1':function(){
				let that=this
				that.$root.eventHub.$emit('pldfssj1')
				//修改设备名称
				this.$ajax.put(this.pld_global.globalurl+'/room/devices',{
					agreementID:that.pldlist[that.pldgetid].pldagreementid,
					details:'',
					devName:that.pld_global.pldiptval,
					id:that.pldlist[that.pldgetid].id,
					roomID:that.pldlist[that.pldgetid].RoomID,
					status:true
				})
				.then(function(res){
					that.pldcloseall()
					that.pldlist[that.pldgetid].test=res.data.Data.DevName
				})
				.catch(function(){
					
				})
				//添加关联设备
				this.$ajax.post(this.pld_global.globalurl+'/room/devices/deviceslinks',{
					id:that.pldlist[that.pldgetid].id,
					uid:that.pld_global.plduid
				})
				.then(function(){
					
				})
				.catch(function(){
				})
				
			},
			//温湿度保存
			'baocun':function(){
				let that=this
				that.$root.eventHub.$emit('pldfssj2')
				this.$ajax.put(this.pld_global.globalurl+'/room/devices',{
					agreementID:that.pldlist[that.pldgetid].pldagreementid,
					details:'',
					devName:that.pld_global.pldiptval,
					id:that.pldlist[that.pldgetid].id,
					roomID:that.pldlist[that.pldgetid].RoomID,
					status:true
				})
				.then(function(res){
//					that.pldcloseall()
					that.pldlist[that.pldgetid].test=res.data.Data.DevName
				})
				.catch(function(){
					
				})
				
				this.$root.eventHub.$emit('pldbaocun',that.pldlist[that.pldgetid].deveui,that.pldlist[that.pldgetid].id,that.pldcloseall())
			},
			//给默认下一步禁用解除
			'plddis':function(){
				this.masks[2][0].dd=false
			},
			//给下一步禁用
			'plddismr':function(){
				this.masks[2][0].dd=true
			},
			'updatetime':function(){
				console.log('保存时间')
			},
			//设备点击
			'pldsevclk':function(data){
				if(this.pldlist[data].show==true){
					this.pldlist[data].show=false
				}else{
					this.pldlist[data].show=true
				}
				
			},
			//调用关联设备弹窗
			'testfucpeople':function(data){
				if(data==0){
					this.tcshow=true
					this.masks=this.peopledate
					this.pldequipcom='allsetting0'
				}else{
					this.tcshow=true
					this.masks=this.plddelete
					this.pldequipcom='deletes'
				}
			},
			'pldrealli':function(data,dataid){
				console.log(dataid)
				let that=this
				that.pldgetid=dataid
				that.pld_global.deviceid=that.pldlist[dataid].id
				that.pld_global.agreementid=that.pldlist[dataid].pldagreementid
				if(data==0){
					this.tcshow=true
					let pldallsz=[
						{
							pldtit:this.pldupdatadate,
							pldtem:'temperature0'
						},
						{
							pldtit:this.peopledate,
							pldtem:'allsetting0'
						},
						{
							pldtit:this.pldupdatadate1,
							pldtem:'allsetting2'
						},
						{
							pldtit:this.pldupdatadate2,
							pldtem:'temperature1'
						},
					]
					this.masks=this.pld_global.pldusetc(that.pldlist[dataid].pldagreementid,pldallsz).pldtit
					this.pldequipcom=this.pld_global.pldusetc(that.pldlist[dataid].pldagreementid,pldallsz).pldtem
					that.masks[0][0].title=that.pldlist[that.pldgetid].test
				}else{
					this.tcshow=true
					this.masks=this.plddelete
					this.pldequipcom='deletes'
					this.plddeletesj=that.dataid
					that.plddelete[0][0].title=that.pldlist[that.pldgetid].test
				}
			},
			//确定删除设备
			'plddeletereal':function(){
				let that=this
				this.$ajax.delete(this.pld_global.globalurl+'/room/devices',{
					data:{
						id:that.pldlist[that.pldgetid].id
					},
			      	headers:{
			      		'Accept': 'application/json',
			      		"maxiiot_user" :this.pld_global.logincookie
			      	}
				})
				.then(function(){
					that.tcshow=false
					that.pldsevice(that.pld_global.roomid,that.pld_global.pldsevice)
				})
				.catch(function(){
					
				})
			},
			//设备开关
			'myfunc':function(data){
				let that=this
				let pldval=1
				that.pldbj=data
				if(that.pldlist[data].pldclat=='pldsectopione'){
					pldval=1
				}else{
					pldval=2
				}
				this.$ajax.post(this.pld_global.globalurl+'/downLinks',{
					devEUI:that.pldlist[data].deveui,
					type:pldval
				})
				.then(function(){
					if(that.pldlist[data].pldclat=='pldsectopione'){
						that.pldlist[data].pldclat='pldsectopi'
//						that.pldlist[data].litextt='已开启'
					}else{
						that.pldlist[data].pldclat='pldsectopione'
//						that.pldlist[data].litextt='已关闭'
					}
				})
				.catch(function(){
					alert('指令下行不成功')
				})
				
			},
			'pldxstc':function(){
				this.tcshow=true
				this.masks=this.privatemasks
				this.pldequipcom='allequipment'+this.plddata
				this.pldtestall=this.privatepldtestall
			},
			//消息中心点击
			'pldpamath':function(){
				this.plderror[0].kg=false
				this.plderror[1].kg=true
			},
			'pldcloseall':function(){
				this.tcshow=false
			},
			'pldchangebtn':function(data){
				for(var i=0;i<this.masks[1].length;i++){
					this.masks[1][i].cla=''
				}
				
				this.masks[1][data]['cla']='pldcla'
				
				this.pldequipcom='allequipment'+data
				this.plddata=data
			},
			'pldchangebtno':function(data){
				if(data==2){
					this.masks=this.pldtemperature
					this.pldequipcom='allsetting2'
					for(var i=0;i<this.masks[1].length;i++){
						this.masks[1][i].cla=''
					}
					
					this.masks[1][data]['cla']='pldcla'
					this.plddata=data
					return
				}else if(data==0){
					this.masks=this.pldupdatadate
				}
				for(var i=0;i<this.masks[1].length;i++){
					this.masks[1][i].cla=''
				}
				
				this.masks[1][data]['cla']='pldcla'
				
				this.pldequipcom='temperature'+data
				this.plddata=data
			},
			'pldchangebtno22':function(data){
				if(data==0){
					this.masks=this.pldupdatadate2
					this.pldequipcom='temperature1'
				}else if(data==1){
					this.masks=this.pldupdatadate3
					this.pldequipcom='temperature2'
				}
				for(var i=0;i<this.masks[1].length;i++){
					this.masks[1][i].cla=''
				}
				
				this.masks[1][data]['cla']='pldcla'
				
				this.plddata=data
			},
			'pldchangebtnpeople':function(data){
				if(data==0){
					this.masks=this.peopledate
				}else if(data==1){
					this.masks=this.peopledatet
				}
				for(var i=0;i<this.masks[1].length;i++){
					this.masks[1][i].cla=''
				}
				
				this.masks[1][data]['cla']='pldcla'
				
				this.pldequipcom='allsetting'+data
				this.plddata=data
			},
			'pldfangfa':function(data,moreff){
				if(!moreff){
					return
				}else{
					moreff(data)
				}
			},
			//让添加成功弹窗显示三秒方法
			'addtc':function(){
				let that=this
				this.textshow=true
				setTimeout(function(){
					that.textshow=false
				},3000)
			},
			'pldtesto':function(){
				this.masks=this.maskso
				this.pldequipcom=add
				this.pldtestall=this.pldtestallo
				this.plddismr()
			},
			'pldtestt':function(){
				this.tcshow=false
			},
			//弹窗上一步
			'pldprev':function(){
				this.masks=this.privatemasks
				this.pldequipcom='allequipment'+this.plddata
				this.pldtestall=this.privatepldtestall
				this.plddismr()
			},
			//弹窗完成
			'pldfin':function(){
				let that=this
				this.$ajax.post(this.pld_global.globalurl+'/room/devices',{
					DevEUI:this.pld_global.addequipment.deveui,
					agreementID:this.pld_global.addequipment.agreement,
					devName:this.pld_global.addequipment.devname,
					roomID:this.pld_global.addequipment.roomid,
					status:true
				})
				.then(function(){
					that.pldsevice(that.pld_global.roomid,that.pld_global.pldsevice)
					that.tcshow=false
					that.addtc()
				})
				.catch(function(){
					that.$root.eventHub.$emit('pldpshow')
				})
			},
			'testfuc':function(){
				alert('test')
			},
			//触发传给子组件的值更新
			'pldupdateo':function(index,data){
				data()
			},
			//点击左侧导航栏恢复默认
			'pldleft':function(){
				this.plderror[0].kg=true
				this.plderror[1].kg=false
			},
			//默认获取房间下的设备（默认是主卧）
			'pldsevice':function(data,edata){
				console.log('index')
				let that=this
				this.$ajax.get(this.pld_global.globalurl+'/room/devices',{
					params:{
						roomID:that.pld_global.roomid,
						type:edata
					},
			      	headers:{
			      		'Accept': 'application/json',
			      		"maxiiot_user" :this.pld_global.logincookie
			      	}
				})
				.then(function(res){
					let pldcs=''
					that.pldlist.splice(0,that.pldlist.length)
					for(let i=0;i<res.data.Data.length;i++){
						let pldsevicemore={
							test:'111',
							kgtop:true,
							licoloro:{
								color:'red',
								width:'40%',
								opacity:0
							},
							licolort:{
								color:'#b8b8b8',
								opacity:1,
								display:'block'
							},
							litexto:'已关闭',
							litextt:'已关闭',
							show:false,
							pldrealli:that.pldrealli,
							id:'',
							deveui:'',
							pldclat:'',
							pldurl:'',
							pldagreementid:0,
							RoomID:0
						}
						pldsevicemore.test=res.data.Data[i].DevName
						pldsevicemore.id=res.data.Data[i].ID
						pldsevicemore.deveui=res.data.Data[i].DevEUI
						pldsevicemore.kgtop=res.data.Data[i].Switchs
//						if(res.data.Data[i].ID==26){
//							pldsevicemore.pldrealli=that.testfucpeople
//						}
						//检测状态
						if(res.data.Data[i].Mode==0){
							pldsevicemore.litextt='已关闭'
							pldsevicemore.licolort.color='red'
							pldcs='_0'
						}else if(res.data.Data[i].Mode==1){
							pldsevicemore.litextt='已开启'
							pldcs='_1'
						}
						pldsevicemore.pldurl=res.data.Data[i].AgreementID+pldcs
						pldsevicemore.pldagreementid=res.data.Data[i].AgreementID
						pldsevicemore.RoomID=res.data.Data[i].RoomID
						//检测是否开启
						if(res.data.Data[i].Mode==0){
							pldsevicemore.pldclat='pldsectopione'
						}else{
							pldsevicemore.pldclat='pldsectopi'
						}
						that.pldlist.push(pldsevicemore)
					}
				})
				.catch(function(){
					
				})
			}
		},
		mounted:function(){
			this.$root.eventHub.$emit('pldglometh')
			let that=this
			//根据id返回名称
			that.$root.eventHub.$on('pldgetid',function(dd){
				for(let i=0;i<that.pldlist.length;i++){
					if(dd==that.pldlist[i].id){
						that.pld_global.pldnamemore=that.pldlist[i].test
					}
				}
			})
			
			//给默认下一步禁用解除
			this.$root.eventHub.$on('plddis',function(){
				that.masks[2][0].dd=false
			})
			//给下一步禁用
			this.$root.eventHub.$on('plddismr',function(){
				that.masks[2][0].dd=true
				console.log(that.masks[2][0].dd)
			})
			
			//状态改变更改界面
			this.$root.eventHub.$on('pldchangezt',function(aa,dd){
				for(let i=0;i<that.pldlist.length;i++){
					if(that.pldlist[i].id==aa&&that.pldlist[i].pldagreementid==2){
						if(dd==0){
							that.pldlist[i].litextt='离床'
							that.pldlist[i].pldclat='pldsectopione'
							that.pldlist[i].licolort.color='red'
						}else{
							that.pldlist[i].litextt='在床'
							that.pldlist[i].pldclat='pldsectopi'
							that.pldlist[i].licolort.color='rgb(184, 184, 184)'
						}
						that.$root.eventHub.$emit('pldchangenei',that.pldlist[i].litextt)
					}else if(that.pldlist[i].id==aa){
						if(dd==0){
							that.pldlist[i].litextt='已关闭'
							that.pldlist[i].pldclat='pldsectopione'
							that.pldlist[i].licolort.color='red'
						}else{
							that.pldlist[i].litextt='已开启'
							that.pldlist[i].pldclat='pldsectopi'
							that.pldlist[i].licolort.color='rgb(184, 184, 184)'
						}
					}
//					if(that.pldlist[i].id==aa){
//						if(dd==0){
//							that.pldlist[i].litextt='已关闭'
//							that.pldlist[i].pldclat='pldsectopione'
//							that.pldlist[i].licolort.color='red'
//						}else{
//							that.pldlist[i].litextt='已开启'
//							that.pldlist[i].pldclat='pldsectopi'
//							that.pldlist[i].licolort.color='rgb(184, 184, 184)'
//						}
//					}
				}
			})
			
			
//			this.$root.eventHub.$emit('pldgetroom')
			
			this.$children[0].pldleftclk(0)
			$(document).mouseup(function(e){
			  var _con = $('.plddian');   // 设置目标区域
			  if(!_con.is(e.target) && _con.has(e.target).length === 0){ // Mark 1
			  	$('.pldtcsevice').hide()
			  }
			});
			
		}
	}
</script>

<style lang="less" scoped="scoped">
.plddiv{
	float: left;
	margin-top: 25px;
	width: 88.5%;
    margin-left: 190px;
}
.pldtext{
	position: absolute;
	width: 195px;
	height: 95px;
	background-color: #fff;
	border-radius: 8px;
	box-shadow: 0px 1px 25px 1px rgba(0, 0, 0, 0.1);
	left: 48%;
    top: 33%;
    text-align: center;
    line-height: 95px;
    font-size: 18px;
    color: #0FC50F;
}

</style>


