<template>
	<div class="pldsec col-md-3 col-xs-3 col-sm-3">
		<div class="pldpad">
			<div class="pldsectop">
				<div class="pldsectopp">
					<span>
						{{title}}
					</span>
				</div>
				<div class="plddian" @click.stop="pldliclk(pldmaths)">
					<span>
						...
					</span>
					<div class="pldtcsevice" v-show="sevicelist[pldmaths].show">
						<ul>
							<li v-for="(it,index) in pldlist" @click="pldrealli(index,pldsevlist)">
								{{it.pp}}
							</li>
						</ul>
					</div>
				</div>
				<div :class="pldallsj[pldsevlist].pldclat" v-show="kgtop" @click.stop="pldkg(pldsevlist)">
					<i>
						
					</i>
					
				</div>
			</div>
			<div class="pldsecimg">
				<img :src="pldimg+pldallsj[pldsevlist].pldurl+'.png'" alt="hongwai"/>
			</div>
			<div class="pldsecshow">
				<ul>
					<li class="pldlileft" :style="licoloro">
						<span>
							{{litexto}}
						</span>
						<i>
							
						</i>
					</li>
					<li :style="licolort">
						<span>
							{{litextt}}
						</span>
					</li>
				</ul>
		</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'sevice',
		props:['title','kgtop','licoloro','licolort','litexto','litextt','sevicelist','pldmaths','pldsevlist','pldallsj'],
		data(){
			return{
				pldimg:'static/img/',
				pldlist:[
					{pp:'设备详情'},
					{pp:'删除设备'}
				],
				pldsectopi:'pldsectopi'
			}
		},
		methods:{
			'pldkg':function(pldsevlist){
//				if(this.pldsectopi=='pldsectopi'){
//					this.pldsectopi='pldsectopione'
//				}else{
//					this.pldsectopi='pldsectopi'
//				}
				this.$emit('test',pldsevlist)
			},
			'pldliclk':function(pldmaths){
				this.$emit('pldsevclk',pldmaths)
			},
			'pldrealli':function(data,pldsevlist){
				this.$emit('pldrealli',data,pldsevlist)
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldsec{
	overflow: hidden;
	padding-left: 25px;
	border-radius: 8px;
	text-align: center;
	margin-bottom: 25px;
	.pldpad{
		background-color: #ffffff;
		padding: 18px;
		.pldsecimg{
			img{
				width: 100px;
				height: 100px;
			}
		}
	}
	.pldsectop{
		.pldsectopp{
			width: 280px;
		    overflow: hidden;
		    text-overflow: ellipsis;
		    word-break: break-word;
		    white-space: nowrap;
			float: left;
		}
		.pldsectopi{
			cursor: pointer;
			float: right;
			width: 40px;
			height: 20px;
			background-color: #18d1ff;
			border-radius: 300px;
			overflow: hidden;
			i{
				height: 18px;
				width: 19px;
				float: right;
				border-radius: 50%;
				background-color: #fff;
				position: relative;
				left: -1px;
				top: 1px;
			}
		}
		.plddian{
			font-size: 25px;
			color: #666666;
			float: right;
			margin-top: -16px;
			margin-left: 14px;
			cursor: pointer;
			position: relative;
			.pldtcsevice{
				position: absolute;
				right: 0;
				width: 91px;
				font-size:14px;
				box-shadow: 0px 1px 25px 1px rgba(0, 0, 0, 0.1);
				border-radius:6px;
				top:45px;
				ul{
					li{
						height: 35px;
						line-height: 35px;
						border-bottom: 1px solid #eeeeee;
					}
					li:hover{
						color: #73deff;
					}
				}
			}
		}
		.pldsectopione{
			cursor: pointer;
			float: right;
			width: 40px;
			height: 20px;
			background-color: #999999;
			border-radius: 300px;
			overflow: hidden;
			i{
				height: 18px;
				width: 19px;
				float: left;
				border-radius: 50%;
				background-color: #fff;
				position: relative;
				left: -1px;
				top: 1px;
			}
		}
	}
	.pldsectop:after{
		  content: "020"; 
		  display: block; 
		  height: 0; 
		  clear: both; 
		  visibility: hidden;  
	}
	.pldsecshow{
		margin-top: 20px;
		ul{
			overflow:hidden;
			li{
				width: 50%;
				float: left;
				text-align: left;
				padding-left: 10px;
				height: 20px;
				line-height: 20px;
			}
			.pldlileft{
				text-align: right;
				padding-right: 10px;
				padding-left: 0;
				border-right: 1px solid #eeeeee;
				overflow: hidden;
				i{
					display: block;
					height: 15px;
					width: 15px;
					border-radius: 50%;
					background-color: red;
					float: right;
					position: relative;
					top: 3px;
					left: -6px;
				}
				span{
					float: right;
				}
			}
		}
	}
}
</style>


