<template>
	<div class="pldequipment container-fluid">
		<div class="plddateall">
			<span>
				{{plddatetext[0][0].title}}
			</span>
			<input class="Wdate" :data-riqi='pldnowt' :value="pldnow" type="text" id="d17" onclick="WdatePicker({firstDayOfWeek:1,dateFmt:'yyyy-MM-dd HH:mm:ss',autoPickDate:'true',onpicked:function(){
				var user = document.getElementById ( 'd17' );
				user.setAttribute('data-riqi',$dp.cal.newdate.y+'-'+$dp.cal.newdate.M+'-'+$dp.cal.newdate.d+'T'+$dp.cal.newdate.H+':'+$dp.cal.newdate.m+':'+$dp.cal.newdate.s+'+0'+new Date().getTimezoneOffset()/-60+':00');
				console.log($dp.cal.newdate);}})"/>
				　－　
			<input id="d5222" class="Wdate" :data-riqi='pldnowth' :value="pldnow" type="text" onclick="WdatePicker({autoPickDate:'true',dateFmt:'yyyy-MM-dd HH:mm:ss',onpicked:function(){
				var user = document.getElementById ( 'd5222' );
				user.setAttribute('data-riqi',$dp.cal.newdate.y+'-'+$dp.cal.newdate.M+'-'+$dp.cal.newdate.d+'T'+$dp.cal.newdate.H+':'+$dp.cal.newdate.m+':'+$dp.cal.newdate.s+'+0'+new Date().getTimezoneOffset()/-60+':00');
				console.log($dp.cal.newdate);}})"/>
			<button @click="pldtest">查询</button>
		</div>
		<div class="plddatetable">
			<table>
				<thead>
					<tr>
						<td v-for="(it,index) in plddatetext[1]" v-if="index==0" style="padding-left: 10px;">
							{{it.pp}}
						</td>
						<td v-else>
							{{it.pp}}
						</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(ita,i) in pldtbody">
						<td style="padding-left: 10px;">
							{{ita.wd}}
						</td>
						<td>
							{{ita.sd}}
						</td>
						<td>
							{{ita.dl}}
						</td>
						<td>
							{{ita.time}}
						</td>
					</tr>
				</tbody>
			</table>
		</div>
		<fenye></fenye>
	</div>
</template>

<script>
	import fenye from '@/components/common_model/fenye'
	export default{
		name:'agodate',
		 data() {
            return {
            	pldid:0,
            	pldnow:'',
            	pldnowt:'',
            	pldnowth:'',
            	starttime:'',
            	endtime:'',
            	plddatetext:[
            		[
            			{title:'起止时间：'}
            		],
            		[
            			{pp:'湿度(℃)'},
            			{pp:'温度(%)'},
            			{pp:'电量(%)'},
            			{pp:'时间'}
            		]
            	],
            	pldtbody:[
//          		{wd:'15',sd:'20',dl:'20',time:'2017-12-23'},
//          		{wd:'15',sd:'20',dl:'20',time:'2017-12-23'},
//          		{wd:'15',sd:'20',dl:'20',time:'2017-12-23'},
//          		{wd:'15',sd:'20',dl:'20',time:'2017-12-23'},
//          		{wd:'15',sd:'20',dl:'20',time:'2017-12-23'}
            	]
            }
        },
		components:{
			fenye
		},
		mounted:function(){
			function getNow(s) {
			    return s < 10 ? '0' + s: s;
			}
			
			var myDate = new Date();
			//获取当前年
			var year=myDate.getFullYear();
			//获取当前月
			var month=myDate.getMonth()+1;
			//获取当前日
			var date=myDate.getDate(); 
			var h=myDate.getHours();       //获取当前小时数(0-23)
			var m=myDate.getMinutes();     //获取当前分钟数(0-59)
			var s=myDate.getSeconds();  
			
			var now=year+'-'+getNow(month)+"-"+getNow(date)+" "+getNow(h)+':'+getNow(m)+":"+getNow(s);
			var nowt=year+'-'+getNow(month)+"-"+getNow(date)+"T"+getNow(h)+':'+getNow(m)+":"+getNow(s)+'+0'+new Date().getTimezoneOffset()/-60+':00';
			this.pldnow=now
			this.pldnowt=nowt
			this.pldnowth=nowt
			
			let that=this
			function fenyetest(dd,cc){
				that.$root.eventHub.$emit('updatefenye',dd,cc)
			}
			//向数组中添加数据
			function szadd(ll){
				that.pldtbody.push(ll)
				for(let i=0;i<that.pldtbody.length;i++){
					that.pldtbody[i].time=that.pld_global.dateFormat(parseInt(that.pldtbody[i].time))
				}
			}
			
			//将数组清空
			function clearsz(){
				that.pldtbody.splice(0,that.pldtbody.length)
			}
			
			switch (that.pld_global.agreementid){
				case 1:
					that.$root.eventHub.$on('pldfyupdate',function(){
						clearsz()
						
						let pldstarttime=Date.parse($('#d17').attr('data-riqi').replace(/-/g,'/').replace('T',' '))
						let pldendtime=Date.parse($('#d5222').attr('data-riqi').replace(/-/g,'/').replace('T',' '))
						
						let that=this
						this.$ajax.get(this.pld_global.globalurl+'/room/devicesData',{
							params:{
								limit:that.pld_global.pldts,
								offset:that.pld_global.pldoffset,
								startTime:pldstarttime,
								endtime:pldendtime,
								deviceID:that.pld_global.deviceid
							},
					      	headers:{
					      		'Accept': 'application/json',
					      		"maxiiot_user" :this.pld_global.logincookie
					      	}
						})
						.then(function(res){
							fenyetest(res.data.Data[0].Pages,res.data.Data[0].Number)
							for(let i=0;i<res.data.Data[0].Data[0].length;i++){
								let pldty={wd:'',sd:'',dl:'',time:''}
								pldty.wd=res.data.Data[0].Data[0][i]
								pldty.sd=res.data.Data[0].Data[1][i]
								pldty.dl=res.data.Data[0].Data[2][i]
								pldty.time=res.data.Data[0].Time[i]
								szadd(pldty)
								console.log(i+'test')
							}
							
						})
						.catch(function(){
							
						})
					})
					break;
				case 2:
					that.$root.eventHub.$on('pldfyupdate',function(){
						clearsz()
						
						let pldstarttime=Date.parse($('#d17').attr('data-riqi').replace(/-/g,'/').replace('T',' '))
						let pldendtime=Date.parse($('#d5222').attr('data-riqi').replace(/-/g,'/').replace('T',' '))
						
						let that=this
						this.$ajax.get(this.pld_global.globalurl+'/room/devicesData',{
							params:{
								limit:that.pld_global.pldts,
								offset:that.pld_global.pldoffset,
								startTime:pldstarttime,
								endtime:pldendtime,
								deviceID:that.pld_global.deviceid
							},
					      	headers:{
					      		'Accept': 'application/json',
					      		"maxiiot_user" :this.pld_global.logincookie
					      	}
						})
						.then(function(res){
							fenyetest(res.data.Data[0].Pages,res.data.Data[0].Number)
							for(let i=0;i<res.data.Data[0].Data[0].length;i++){
								let pldty={wd:'',sd:'',dl:'',time:''}
								pldty.wd=res.data.Data[0].Data[0][i]
								pldty.sd=res.data.Data[0].Data[1][i]
								pldty.dl=res.data.Data[0].Data[2][i]
								pldty.time=res.data.Data[0].Time[i]
								szadd(pldty)
								console.log(i+'test')
							}
							
						})
						.catch(function(){
							
						})
					})
					break;
				default:
					break;
			}
			
		},
		methods:{
			'pldstart':function(){
				
			},
			'pldtest':function(){
				let that=this
				switch(that.pld_global.agreementid){
					case 1:
						this.$root.eventHub.$emit('pldfyupdate')	
					break;
					case 2:
						this.$root.eventHub.$emit('pldfyupdate')	
					break;
					default:
					break;
						
				}
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldequipment{
	.active{
		a{
			background-color: #12d1ed;
			color: #fff;
			border: none;
		}
	}
	.plddateall{
		input{
			height: 40px;
			width: 39%;
			border: 1px solid #dbdbdb;
			border-radius: 6px;
			padding-left:15px;
			padding-right:25px;
		}
		.Wdate{
			background-position:97% 3px;
			background-size: 3% 90%;
		}
		button{
			height: 40px;
			width: 90px;
			background-color: #18d1ff;
			color: #fff;
			outline: none;
			border: none;
			border-radius: 8px;
			font-size: 16px;
			margin-left: 18px;
		}
	}
	.plddatetable{
		width: 98%;
	    max-height: 385px;
	    overflow-y: auto;
		table{
			margin-top: 30px;
			width:100%;
			tr{
				height: 35px;
				line-height: 35px;
			}
			thead{
				tr{
					background-color: #f0f2f2;
					color: #a0a0a0;
				}
			}
			tbody{
				tr{
					background-color: #f7fafa;
				}
				tr:nth-child(even){
					background-color: #ffffff;
				}
			}
		}
	}
}
</style>


