<template>
	<div class="pldbd">
		<ul>
			<li v-for="(ii,ind) in pldbdroom" v-show="ii.kg" :style="{borderRight:ii.bordertext}">
				<img src="static/img/bedroom.png" alt="bedroom" v-show="ii.imgshow"/>
				<div class="pldli">
					<span>
						{{ii.wendu}}
					</span>
					<p>
						{{ii.wdtext}}
					</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		name:'bedroom',
		data(){
			return{
				pldbdroom:[
					{'imgshow':true,'wendu':'温度','wdtext':'25℃','bordertext':'1px solid #eeeeee',kg:false},
					{'imgshow':true,'wendu':'湿度','wdtext':'25℃','bordertext':'1px solid #eeeeee',kg:false},
					{'imgshow':true,'wendu':'pm2.5','wdtext':'25℃','bordertext':'0px solid #eeeeee',kg:false}
				]
			}
		},
		methods:{
			'pldbr':function(data){
				let that=this
				that.$ajax.get(this.pld_global.globalurl+'/roomdata',{
					params:{
						ID:data
					},
			      	headers:{
			      		'Accept': 'application/json',
			      		"maxiiot_user" :this.pld_global.logincookie
			      	}
				})
				.then(function(res){
					console.log(res.data.Data.humi)
					if(res.data.Data.humi!=undefined){
						that.pldbdroom[0].kg=true
					}else{
						that.pldbdroom[0].kg=false
					}
					if(res.data.Data.temp!=undefined){
						that.pldbdroom[1].kg=true
					}else{
						that.pldbdroom[1].kg=false
					}
					if(res.data.Data.pm25!=undefined){
						that.pldbdroom[2].kg=true
					}else{
						that.pldbdroom[2].kg=false
					}
					that.pldbdroom[0].wdtext=res.data.Data.humi
					that.pldbdroom[1].wdtext=res.data.Data.temp
					that.pldbdroom[2].wdtext=res.data.Data.pm25
	//				for(let i=0;i<res.data.Data.length;i++){
	//					pldlist.wdtext=res.data.Data.
	//				}
				})
				.catch(function(){
					
				})
			}
		},
		mounted:function(){
			let that=this
			
			this.$root.eventHub.$on('pldbdroom',function(data){
				that.pldbr(data)
			})
			that.pldbr(that.pld_global.roomid)
			
		}
	}
</script>

<style lang="less" scoped="scoped">
.pldbd{
	height: 165px;
    background-color: #fff;
    float: left;
    width: 87%;
    margin-left: 21px;
    overflow: hidden;
    padding-left: 18px;
    margin-top: 105px;
    border-radius: 9px;
    margin-left: 215px;
    img{
    	float: left;
    	margin-top: -13px;
    }
    ul{
    	li{
    		margin-right: 40px;
    		float: left;
    		margin-top: 42px;
    		width: 12%;
    		height:81px;
    		/*border-right: 1px solid #eeeeee;*/
    		min-width: 260px;
    		line-height: 53px;
    		.pldli{
		    	float: left;
		    	margin-left: 6px;
		    	margin-top: -13px;
		    	p{
		    		font-size: 40px;
    				font-weight: bold;
		    	}
		    }
    	}
    }
}
</style>


