<template>
  <div class="paginate_container pldfenye">
  	<span class="pldspanone">
  		{{pldjl[0].pp}}{{allmath}}{{pldjl[1].pp}}
  	</span>
  	<div class="pldsec">
  		<select @change="pldselect($event)">
	  		<option v-for="(itt,iidex) in pldmath"  ref="iidex">
	  			{{itt.pp}}{{pldjl[2].pp}}
	  		</option>
  		</select>
  		<img src="static/img/bottom_small.png" alt="bottom_small" />
  	</div>
    <paginate
      :page-count="pageCount"
      :margin-pages="marginPages"
      :page-range="rangePage"
      :initial-page="initPage"
      :click-handler="pageEvent"
      prev-text="上一页"
      next-text="下一页"
      :container-class="'pagination'"
      :page-class="'page-item'"
      :page-link-class="'page-link-item'"
      :prev-class="'prev-item'"
      :prev-link-class="'prev-link-item'"
      :next-class="'next-item'"
      :next-link-class="'next-link-item'"
    ></paginate>
    <span class="pldfysp">
    	{{pldjl[3].pp}}
    </span>
    <input type="text" class="pldfyipt" />
    <button>
    	Go
    </button>
  </div>
</template>

<script>
import paginate from "vuejs-paginate"
export default {
	name:'fenye',
    components:{
		paginate
	},
    data(){
    	return{
    		pldxlvalue:'',
    		pageCount:1,
        marginPages:1,
        rangePage:this.pld_global.pldts,
        initPage:1,
        allmath:'0',
        pldjl:[
    			{pp:'共'},
    			{pp:'条记录'},
    			{pp:'条/页'},
    			{pp:'跳转到'}
    		],
    		pldmath:[
    			{pp:'10'},
    			{pp:'20'},
    			{pp:'30'}
    		]
    	}
    },
	  methods: {
	    pageEvent: function(e) {
	      console.log(e);
	      this.pld_global.pldoffset=e*this.pld_global.pldts+1
	      this.$root.eventHub.$emit('pldfyupdate')
//	      this.$emit("pageEvent", e); //将值放在自定义的事件函数中作为参数
	    },
	    pldselect:function(event){
	    	let that=this
	    	let pldzz
	    	for(let i=0;i<that.pldmath.length;i++){
	    		if(that.pldmath[i].pp==event.target.value.slice(0,2)){
	    			pldzz=i
	    		}
	    	}
	    	that.pld_global.pldts=parseInt(that.pldmath[pldzz].pp)
	    	that.$root.eventHub.$emit('pldfyupdate')
	    }
	  },
	  mounted:function(){
	  	let that=this
	  		this.$root.eventHub.$on('updatefenye',function(dd,cc){
					that.pageCount=parseInt(dd)
					that.allmath=cc
					console.log(cc)
				})
	  }
  }
  
</script>
<style scoped="scoped" lang="less">
.paginate_container {
	margin-top: 80px;
  float: right;
  padding-right: 15px;
  .pagination{
  	margin: 0 0;
  	position: relative;
  	top: 10px;
  }
  .pldsec{
  	display: inline-block;
  	width: 90px;
  	position: relative;
  	img{
  		position: absolute;
  		right: 8px;
  		top: 15px;
  	}
  	select{
  		width: 100%;
  		border: 1px solid #dddddd;
  		height: 35px;
  		text-align: center;
  		color: #808080;
  		padding-left: 10px;
  		option{
  			width: 100%;
  			text-align: center;
  		}
  	}
  }
  span{
  	color: #a9a9a9;
  }
  .pldspanone{
  	margin-right: 10px;
  }
  .pldfysp{
  	margin-left: 10px;
  	margin-right: 3px;
  }
  .pldfyipt{
  	width: 30px;
  	height: 30px;
  	border: 1px solid #dddddd;
  	border-radius: 6px;
  	margin-right: 3px;
  }
  button{
  	background-color: #12d1ed;
  	border: none;
  	border-radius: 4px;
  	height: 29px;
  	width: 40px;
  	color: #fff;
  	font-size: 16px;
  	margin-left: 6px;
  	margin-right: 10px;
  	
  }
}

.page-item {
}
.page-link-item {
}
.prev-item {
}
.prev-link-item {
}
.next-item {
}
.next-link-item {
}
</style>



